<template>
  <el-checkbox-group
    :class="props.direction ? `el-checkbox-group_${props.direction}` : ''"
    :model-value="props.modelValue"
    @change="handleChange"
  >
    <el-checkbox-button
      v-for="(item, index) in props.list"
      :value="item.value"
      :key="index"
      >{{ item.label }}
    </el-checkbox-button>
  </el-checkbox-group>
</template>

<script setup lang="ts">
const props = defineProps<{
  list: any[];
  type?: string;
  modelValue: any[];
  direction?: "column" | "row";
}>();
const emits = defineEmits<{
  (e: "update:modelValue", value: any);
}>();

function handleChange(value) {
  emits("update:modelValue", value);
}
</script>

<style scoped lang="scss">
.el-checkbox-group_row,
.el-checkbox-group_column {
  display: flex;
  flex-direction: v-bind(direction);

  :deep(.is-checked__inner) {
    border-color: #0056ff;
  }
  :deep(.el-checkbox-button__inner) {
    min-width: 100px;
    border-radius: 4px;
    border: 1px solid #dcdfe6;
    box-shadow: none;
  }
}

.el-checkbox-group_row {
  flex-wrap: wrap;
  :deep(.el-checkbox-button) {
    margin-right: 15px;
    margin-bottom: 10px;
  }
}

.el-checkbox-group_column {
  :deep(.el-checkbox-button) {
    margin-bottom: 10px;
  }
  :deep(.el-checkbox-button__inner) {
    word-break: break-all;
    white-space: break-spaces;
  }
}
</style>
