// 获取我的客户列表
export const allCustomerApi = {
  getData: (params) => {
    return http.get(`${serviceName.marketAdmin}/customer/my`, {
      params: params,
    });
  },
  handleData: (data) => {
    return {
      list: arrayHasValue(data.records)
        ? data.records.map((item) => {
            item.createdTime = dayjs(item.createdTime).format(
              "YYYY-MM-DD HH:mm:ss"
            );
            item.showDetail =
              item.businessType !== 1 &&
              item.serviceStatus !== 2 &&
              item.companyId;
            item.founderAccount = ["数企系统服务"].includes(
              item.businessTypeName
            )
              ? item.createdByAccount
              : "-";
            item.founderName = ["数企系统服务"].includes(item.businessTypeName)
              ? item.createdByName
              : "-";
            return item;
          })
        : [],
      total: data.total,
    };
  },
};
