// 获取我的客户详情
interface CustomerDetail {
  parentId: string;
  companyId: string;
}

export const customerDetailApi = {
  getData: (params: CustomerDetail) => {
    return http.get(`${serviceName.marketAdmin}/customer/detail`, {
      params: params,
    });
  },
  handleData: function (data) {
    data = judgeDataType(data) === "[object Object]" ? data : {};
    const chinaData = new EluiChinaAreaDht.ChinaArea().chinaAreaflat;
    let companyAddress = "--";
    if (data?.areaCode) {
      const districtData = chinaData[data?.areaCode];
      const cityAreaData = chinaData[districtData.parent];
      const provinceData = chinaData[cityAreaData.parent];
      const districtName = districtData.label;
      const cityName = cityAreaData.label;
      const provinceName = provinceData.label;
      companyAddress = `${provinceName}/${cityName}/${districtName}`;
    }
    return {
      ...data,
      companyNameStr: data?.companyName || "未知",
      companyTypeStr: data?.companyType || "--",
      companySourceStr: data?.companySource || "邀请",
      industryNameStr: data?.industryName || "--",
      registeredCapitalStr: data?.registeredCapital || "--",
      contactPersonStr: data?.contactPerson || "--",
      contactPhoneStr: data?.contactPhone || "--",
      addrDescStr: data?.addrDesc || "--",
      serviceStatusNameStr: data?.serviceStatusName || "--",
      certificationStatusStr:
        data?.certificationStatus === 1 ? "已认证" : "未认证",
      companyAddress,
      areaCode: data?.areaCode,
    };

    // return {
    //   createdByName: data?.createdByName,
    //   createdByAccount: data?.createdByAccount,
    //   industrId: data?.industrId,
    //   companyName: data?.companyName ?? "未知",
    //   companyType: data?.companyType ?? "--",
    //   companySource: data?.companySource ?? "邀请",
    //   industryName: data?.industryName,
    //   registeredCapital: data?.registeredCapital ?? "--",
    //   serviceStatusName: data?.serviceStatusName ?? "--",
    //   addrDesc: data?.addrDesc,
    //   areaCode: data?.areaCode,
    //   contactPhone: data?.contactPhone,
    //   contactPerson: data?.contactPerson,
    //   certificationStatus: data?.certificationStatus,
    //   fullAddress: fullAddress,
    // };
  },
};
// 我的客户-----已购服务
export const customerByServeApi = {
  getData: (params: { companyId: number }) => {
    return http.post(
      `${serviceName.marketVip}/consumption/queryCompanyResource?companyId=${params.companyId}`
    );
  },
  handleData: (data) => {
    return arrayHasValue(data)
      ? data.map((item) => {
          const productSpecsInfo = handleSpecsCode(item.consumptionTypeCd);
          let owned: number, // 总数量
            used: number; // 已用数量
          if (item.consumptionTypeCd == "STORE") {
            owned = bToGB(item.owned);
            used = bToGB(item.used);
          } else if (item.consumptionTypeCd == "HWCZ") {
            owned = item.owned / 1000;
            used = item.used / 1000;
          } else {
            owned = item.owned;
            used = item.used;
          }
          const residue = owned - used; // 剩余数量
          return {
            isRenew:
              [
                // "NECESSAYRY_CUM", // 客户增量
                // "PACKAGE_CALL_SEAT", // 鱼鹰呼叫软件
                "CLOUD_CALL_PACKAGE", // 鱼鹰呼叫套餐
              ].includes(item?.serviceCode) && item.refund !== 1,
            used: used,
            owned: owned,
            residue: residue,
            startTime:
              item.startTime &&
              dayjs(item.startTime).format("YYYY-MM-DD HH:mm"),
            endTime: !indefiniteCodeList.includes(item?.consumptionTypeCd)
              ? item.endTime && dayjs(item.endTime).format("YYYY-MM-DD HH:mm")
              : "永久有效",
            unit: productSpecsInfo.unit,
            addedCodes: item?.consumptionTypeCd,
            combineServiceName: item.resourceName,
          };
        })
      : [];
  },
};
// 我的客户-----已购产品
export const customerByProductApi = {
  getData: (params: { companyId: number }) => {
    return http.post(
      `${serviceName.marketVip}/consumption/queryCompanyService?companyId=${params.companyId}`
    );
  },
  handleData: (data) => {
    return arrayHasValue(data)
      ? data.map((item) => {
          return {
            isRenew:
              [
                "PACKAGE_CALL_SEAT",
                "CLOUD_CALL_PACKAGE",
                "CLOUD_CALL_SOFT",
                "WEB_CUSTOMER_PHONE",
                "CLOUD_CUSTOMER_21S", //21秒云集客
              ].includes(item?.combineServiceCode) && item.refund !== 1,
            userNumber: item?.userNumber ?? 0,
            paramType: item.combineService?.paramType,
            calculateType: item.combineService?.calculateType,
            addedCodes: item.combineService?.addedCodes,
            combineServiceCode: item?.combineServiceCode,
            combineServiceName: item?.combineService?.combineServiceName,
            combineServiceTitle: item?.combineService?.combineServiceTitle,
            startTime:
              item.startTime &&
              dayjs(item.startTime).format("YYYY-MM-DD HH:mm"),
            endTime:
              item.endTime && dayjs(item.endTime).format("YYYY-MM-DD HH:mm"),
          };
        })
      : [];
  },
};

// 我的客户----交易订单返佣
interface CustomerCommission {
  endTime: string;
  startTime: string;
  orderNo: string;
  orderState: number;
  customerId: string;
}

export const customerCommissionApi = {
  getData: (params: CustomerCommission) => {
    return http.post(
      `${serviceName.agentSystem}/commission/customerCommissionList`,
      params
    );
  },
  handleData: function (data) {
    return {
      list: arrayHasValue(data.records)
        ? data.records.map((item) => {
            const orderStatusInfo = arrayMatchValue(
              orderStatusList,
              item.orderState,
              "value"
            );
            let cycle = ""; //周期
            if (item.dateType === 1) {
              cycle = `${item.dateValue}日`;
            } else if (item.dateType === 2) {
              cycle = `${item.dateValue}月`;
            } else if (item.dateType === 3) {
              cycle = `${item.dateValue}年`;
            }
            return {
              id: item.id,
              paramType: item.paramType,
              amountTime:
                item.amountTime &&
                dayjs(item.amountTime).format("YYYY-MM-DD HH:mm:ss"),
              proxyPrice: item.proxyPrice,
              buyTime:
                item.buyTime &&
                dayjs(item.buyTime).format("YYYY-MM-DD HH:mm:ss"),
              appName: item.appName,
              orderNo: item.orderNo,
              realPrice: item.realPrice,
              amount: item.amount,
              orderState: item.orderState,
              cycle: cycle,
              orderStateName: orderStatusInfo?.label,
              orderStateStyleType: orderStatusInfo?.styleType,
            };
          })
        : [],
      total: parseInt(data.total) ?? 0,
    };
  },
};

// 获取我的服务商列表
interface PersonalAgencyList {
  agencyLevel: number;
  agencyName: string;
  current: number;
  size: number;
}

export const personalAgencyListApi = {
  getData: (params: PersonalAgencyList) => {
    return http.post(`${serviceName.agentSystem}/user/myAgencyList`, params);
  },
  handleData: (data) => {
    return {
      list: arrayHasValue(data.records)
        ? data.records.map((item) => {
            const agencyLevelInfo = arrayMatchValue(
              agencyLevelList,
              item.agencyLevel,
              "value"
            );
            return {
              agencyId: item.agencyId,
              source: item.source,
              sourceName: "邀请",
              userName: item.userName,
              mobilePhone: item.mobilePhone,
              seriesName: item.seriesName,
              parentName: item.parentName,
              mode: item.mode,
              modeName: agentTypeMap.get(item.mode),
              agencyLevel: item.agencyLevel,
              agencyLevelName: agencyLevelInfo?.label,
              createdTime:
                item.createdTime &&
                dayjs(item.createdTime).format("YYYY-MM-DD HH:mm"),
            };
          })
        : [],
      total: parseInt(data.total),
    };
  },
};

