// 我的服务商--已购买产品
interface AgencyCommission {
  endTime: string;
  startTime: string;
  orderNo: string;
  orderState: number | string;
  size: number;
  current: number;
  agencyId: unknown;
}

export const agencyCommissionApi = {
  getData: (params: AgencyCommission) => {
    return http.post(
      `${serviceName.agentSystem}/commission/agencyCommissionList`,
      params
    );
  },
  handleData: function (data) {
    return {
      list: arrayHasValue(data.records)
        ? data.records.map((item) => {
            const orderStatusInfo = arrayMatchValue(
              orderStatusList,
              item.orderState,
              "value"
            );
            item.orderStateName = orderStatusInfo?.label;
            item.buyTime =
              item.buyTime && dayjs(item.buyTime).format("YYYY-MM-DD HH:mm:ss");
            return item;
          })
        : [],
      total: parseInt(data.total),
    };
  },
};
