//跟进列表
export const followUpListApi = {
  getData: (params) => {
    return http.post(
      `${serviceName.agentSystem}/agency-follow-up/pageList`,
      params,
      contentType()
    );
  },
  handleData: (data) => {
    console.log(data);
    return {
      list: arrayHasValue(data.records)
        ? data.records.map((item) => {
            const attachment = item.attachment
              ? item.attachment.split(",").filter((url) => url.trim() !== "")
              : [];

            return {
              followContent: item.followContent,
              createdTime: item.createdTime,
              attachment,
            };
          })
        : [],
      total: parseInt(data.total),
    };
  },
};
