<template>
  <el-drawer
    :size="600"
    class="custom-drawer"
    :model-value="initData.visible"
    :title="initData.type === 'add' ? '新增模板' : '编辑模板'"
    :before-close="handleClose"
  >
    <div class="body-container">
      <el-form
        ref="ruleFormRef"
        :model="formData"
        :rules="rules"
        label-position="top"
      >
        <el-form-item label="标题" prop="templateTitle">
          <el-input v-model="formData.templateTitle"></el-input>
        </el-form-item>
        <el-form-item
          v-if="initData.businessType === 0"
          label="分组"
          prop="templateGroup"
        >
          <div class="add-group">
            <el-select v-model="formData.templateGroup">
              <el-option
                v-for="(item, index) in initData.templateGroupList"
                :key="index"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
            <el-button type="primary" @click="handleTemplateGroup"
              >添加</el-button
            >
          </div>
        </el-form-item>
        <el-form-item label="模板" prop="templateContent">
          <el-input
            type="textarea"
            :autosize="{
              minRows: 20,
              maxRows: 26,
            }"
            :placeholder="'请严格按照对话框格式填写模板内容，否则将无法通过审核。\n例如：\n坐席：您好，我是XX公司的XX，看到您在XX上给我们留了言，给您做一个回访；\n客户：好，你说；\n坐席：...\n\n注：话术中公司名称必须为公司全称、字数需大于150字小于750字。'"
            v-model="formData.templateContent"
            :rows="20"
          ></el-input>
          <p class="remark">
            备注：提交后将由数企进行审核，审核通过后请按照报备话术进行通话。
            <span>{{ templateLength }}/750</span>
          </p>
        </el-form-item>
        <el-form-item label="话术关键词（须为模板中的内容）">
          <div class="add-word">
            <el-input
              v-model="formData.word"
              maxlength="10"
              show-word-limit
              type="text"
            />
            <el-button type="primary" @click="handleAddWord">添加</el-button>
          </div>
          <div class="word-list" v-show="formData.wordList.length">
            <el-tag
              v-for="(word, index) in formData.wordList"
              :key="index"
              closable
              @close="handleRemoveWord(index)"
              >{{ word }}</el-tag
            >
          </div>
          <p class="remark">
            设置您话术中的关键词，帮助AI更快理解您的意思，加速您的审核速度，保障您的通话稳定性。
          </p>
        </el-form-item>
      </el-form>
    </div>
    <template #footer>
      <div class="footer">
        <el-button type="primary" @click="handleSubmit">确认</el-button>
        <el-button @click="handleClose">关闭</el-button>
      </div>
    </template>
    <AddSpeechLibraryGroupDialog
      ref="addSpeechLibraryGroupRef"
      @emitFunc="getTemplateGroupList"
    ></AddSpeechLibraryGroupDialog>
  </el-drawer>
</template>
<script setup lang="ts">
import { ElMessage } from "element-plus";
import {
  ref,
  nextTick,
  computed,
  reactive,
  shallowReactive,
  defineAsyncComponent,
} from "vue";
import {
  addSpeechLibraryApi,
  editSpeechLibraryApi,
  speechLibraryGroupListApi,
} from "@/api/business-management-api";
import { useVueRouter } from "@assets/useHook";
import { LocalStorage } from "jz-tool-lib";

const AddSpeechLibraryGroupDialog = defineAsyncComponent(
  () => import("./add-speech-library-group-dialog.vue")
);

const emits = defineEmits<{
  (e: "emitFunc"): void;
}>();
const templateReg = new RegExp(
  "[`~!@#$^&*()=|{}':;',\\[\\].<>/?~！@#￥……&*（）——|{}【】‘；：_+”“'。，、？/\\n]",
  "g"
);
const addSpeechLibraryGroupRef = ref();
const { routeQuery } = useVueRouter();
const { bd_userInfo: userInfo, bd_virCompanyId: virCompanyId } =
  LocalStorage.batchGetItem(["bd_userInfo", "bd_virCompanyId"]);
const ruleFormRef = ref();
const initData = shallowReactive<Record<string, any>>({
  type: "",
  visible: false,
  companyId: "",
  templateId: "",
  businessType: "",
  subCompanyCode: "",
  templateGroupList: [],
});
const formData = reactive({
  word: "",
  wordList: [],
  templateTitle: "",
  templateGroup: "",
  templateContent: "",
});
const templateLength = computed(() => {
  return formData.templateContent.replace(templateReg, "").length;
});
const rules = {
  templateTitle: [{ required: true, message: "请输入标题", trigger: "blur" }],
  templateGroup: [{ required: true, message: "请选择分组", trigger: "blur" }],
  templateContent: [
    { required: true, validator: validateTemplate, trigger: "blur" },
  ],
};
function validateTemplate(rule: any, value: any, callback: any) {
  if (!value) {
    return callback(new Error("请输入模板内容"));
  }
  // 使用空字符串替换匹配到的标点符号
  let filteredStr = value.replace(templateReg, "");
  if (filteredStr.length < 150) {
    return callback(new Error("话术内容需大于150字，请补充话术内容"));
  }
  if (filteredStr.length > 750) {
    return callback(new Error("话术内容最大750字,请适当删除"));
  }
  callback();
}
// 添加话术关键词
const handleAddWord = () => {
  const word = formData.word;
  if (word) {
    if (/[^\da-zA-Z\u4e00-\u9fa5]/.test(word)) {
      return ElMessage.warning("只能输入数字、字母或者汉字");
    } else if (formData.templateContent.indexOf(word) === -1) {
      return ElMessage.warning("关键词在模版中不存在");
    } else if ((formData.wordList as string[]).includes(word)) {
      return ElMessage.warning("关键词不能重复");
    }
    (formData.wordList as string[]).push(word);
    formData.word = "";
  } else {
    ElMessage.warning("请输入关键词");
  }
};
const handleRemoveWord = (index: number) => {
  formData.wordList.splice(index, 1);
};
// 新增模板分组
function handleTemplateGroup() {
  addSpeechLibraryGroupRef.value.trigger(true);
}
// 获取模板分组列表
async function getTemplateGroupList() {
  const { data } = await speechLibraryGroupListApi.getData({
    companyId: initData.companyId,
  });
  initData.templateGroupList = speechLibraryGroupListApi.handleData(data);
}
// 提交话书库
function handleSubmit() {
  if (!ruleFormRef.value) return;
  ruleFormRef.value.validate(async (valid) => {
    if (valid) {
      let speechLibraryApi = addSpeechLibraryApi;
      const isBusinessType = initData.businessType === 0;
      const params = {
        words: formData.wordList.toString(),
        title: formData.templateTitle,
        content: formData.templateContent,
        profileId: isBusinessType
          ? routeQuery.value.profileId
          : userInfo.profileId,
      };
      if (isBusinessType) {
        Reflect.set(params, "groupId", formData.templateGroup);
      }
      if (initData.type === "add") {
        speechLibraryApi = addSpeechLibraryApi;
        Reflect.set(
          params,
          "companyId",
          isBusinessType ? initData.companyId : virCompanyId
        );
        Reflect.set(params, "companyCode", initData.subCompanyCode);
      } else if (initData.type === "edit") {
        speechLibraryApi = editSpeechLibraryApi;
        Reflect.set(params, "id", initData.templateId);
      }
      await speechLibraryApi.getData(params);
      ElMessage.success(initData.type === "add" ? "新增成功" : "编辑成功");
      emits("emitFunc");
      handleClose();
    } else {
      return false;
    }
  });
}
/*
 * 关闭弹框
 * */
function handleClose() {
  initData.type = "";
  initData.companyId = "";
  initData.templateId = "";
  initData.businessType = "";
  initData.subCompanyCode = "";
  formData.word = "";
  formData.wordList = [];
  ruleFormRef.value.resetFields();
  initData.visible = false;
}
/*
 * 打开抽屉
 * */
function toggle(
  type: "add" | "edit",
  visible: boolean,
  info: Record<string, any>
) {
  initData.type = type;
  initData.visible = visible;
  const infoData = info;
  initData.companyId = infoData.companyId;
  initData.businessType = infoData.businessType;
  initData.subCompanyCode = infoData.subCompanyCode;
  if (type === "edit") {
    initData.templateId = infoData.id;
    nextTick(() => {
      formData.templateTitle = infoData.title;
      formData.templateGroup = infoData.groupId;
      formData.templateContent = infoData.content;
      formData.wordList = infoData.words ? infoData.words.split(",") : [];
    });
  }
  if (initData.businessType === 0) {
    // 获取话术模板分组
    getTemplateGroupList();
  }
}
defineExpose({
  toggle,
});
</script>
<style scoped lang="scss">
.add-group,
.add-word {
  @include flex(center);
  width: 100%;
  .el-button {
    margin: 0 10px;
  }
  .tip {
    font-size: 12px;
    color: #f12525;
  }
}
.word-list {
  @include flex(center);
  width: 100%;
  margin-top: 10px;
  .el-tag {
    margin-right: 8px;
  }
}
.remark {
  @include flex(center, space-between);
  width: 100%;
  color: #646b7a;
  font-size: 13px;
  line-height: 18px;
  margin-top: 10px;
}
</style>
