<template>
  <!-- 新增、编辑发票抬头 -->
  <el-drawer
    v-model="visible"
    size="650"
    title="发票抬头"
    @open="open"
    @close="close"
    class="custom-drawer"
  >
    <el-form
      ref="formRef"
      class="form"
      :model="form"
      :rules="rules"
      label-width="100px"
    >
      <el-form-item label="发票类型：" prop="type">
        <el-radio-group
          :disabled="!!$props.formData?.id"
          v-model="form.type"
          size="large"
        >
          <el-radio-button label="普通发票" :value="1" />
          <el-radio-button label="增值税发票" :value="3" />
        </el-radio-group>
      </el-form-item>
      <el-form-item label="发票抬头：" prop="invoiceTitle">
        <el-select
          v-model="form.invoiceTitle"
          placeholder="请选择发票抬头"
          @change="queryTaxNumber"
        >
          <el-option
            v-for="item in invoiceHeaderOptions"
            :key="item.id"
            :label="item.label"
            :value="item.id"
          />
        </el-select>
      </el-form-item>
      <el-form-item label="发票税号：" prop="creditCodes">
        <el-input
          disabled
          v-model="form.creditCodes"
          placeholder="请输入单位税号"
        />
      </el-form-item>
      <el-form-item label="注册地址：" prop="address">
        <el-input v-model="form.address" placeholder="请输入注册地址" />
      </el-form-item>
      <el-form-item label="注册电话：" prop="phone">
        <el-input v-model="form.phone" placeholder="请输入注册电话" />
      </el-form-item>
      <el-form-item label="开户银行：" prop="bank">
        <el-input v-model="form.bank" placeholder="请输入开户银行" />
      </el-form-item>
      <el-form-item label="银行账户：" prop="account">
        <el-input v-model="form.account" placeholder="请输入银行账户" />
      </el-form-item>
      <el-form-item>
        <span class="tip">若发票抬头错误请联系业务人员</span>
      </el-form-item>
    </el-form>
    <template #footer>
      <el-button @click="visible = false">取消</el-button>
      <el-button type="primary" @click="confirm">确认</el-button>
    </template>
  </el-drawer>
</template>

<script lang="ts" setup>
import { computed, ref, shallowReactive } from "vue";
import { LocalStorage } from "jz-tool-lib";
import { ElMessage } from "element-plus";
import {
  addInvoiceApi,
  editInvoiceApi,
  queryTaxNumberApi,
  queryInvoiceHeaderListApi,
} from "@api/common-api";

const {
  bd_userInfo: userInfo,
  bd_agentInfo: agentInfo,
  bd_virCompanyId: virCompanyId,
} = LocalStorage.batchGetItem([
  "bd_userInfo",
  "bd_agentInfo",
  "bd_virCompanyId",
]);

interface FormDataType {
  id: string;
  type: number;
  invoiceTitle: string;
  creditCodes: string;
  address: string;
  phone: string;
  bank: string;
  account: string;
}

const props = withDefaults(
  defineProps<{
    modelValue: boolean;
    formData?: FormDataType;
  }>(),
  {}
);

const emits = defineEmits<{
  (e: "update:modelValue", val: boolean): void;
  (e: "success"): void;
}>();

const formRef = ref();
const form = shallowReactive({
  type: 1,
  invoiceTitle: "",
  creditCodes: "",
  address: "",
  phone: "",
  bank: "",
  account: "",
});
const invoiceHeaderOptions = ref([]);

const rules = {
  invoiceTitle: [
    { required: true, message: "发票抬头不得为空", trigger: "blur" },
  ],
  creditCodes: [
    { required: true, message: "单位税号不得为空", trigger: "blur" },
  ],
};

const visible = computed({
  get: () => props.modelValue,
  set: (n) => emits("update:modelValue", n),
});

async function queryList() {
  const params = {
    name: agentInfo.companyName,
  };
  try {
    const res = await queryInvoiceHeaderListApi.getData(params);
    invoiceHeaderOptions.value = queryInvoiceHeaderListApi.handleData(res.data);
  } catch (e) {
    console.error("获取发票抬头列表失败", e);
  }
}

async function queryTaxNumber() {
  // TODO: 暂时处理，没有精线索ID不获取税号
  if (!form.invoiceTitle) return;
  const params = {
    id: form.invoiceTitle,
  };
  try {
    const res = await queryTaxNumberApi(params);
    form.creditCodes = res.data;
  } catch (e) {
    console.error("获取发票税号失败", e);
  }
}

function confirm() {
  formRef.value.validate(async (valid) => {
    if (valid) {
      const API = props.formData?.id ? editInvoiceApi : addInvoiceApi;
      const invoiceTitle = invoiceHeaderOptions.value.find(
        (item) => item.id === form.invoiceTitle
      )?.label;
      const params = {
        id: props.formData?.id || "",
        addModel: 2, // 代理商添加
        companyId: virCompanyId,
        profileId: userInfo.profileId,
        profileName: userInfo.profileName,
        ...form,
        invoiceTitle,
      };
      const { statusCode, msg } = await API(params);
      if (statusCode) {
        return ElMessage.warning(msg || "操作失败");
      }
      ElMessage.success(msg || "操作成功");
      visible.value = false;
      emits("success");
    }
  });
}

async function open() {
  await queryList();
  initForm();
  if (!props.formData?.id) {
    await queryTaxNumber();
  }
}

/**
 * 初始化表单数据
 */
function initForm() {
  if (props.formData?.id) {
    for (const key in form) {
      form[key] = props.formData[key];
    }
    form.invoiceTitle = invoiceHeaderOptions.value.find(
      (item) => item.label === form.invoiceTitle
    )?.id;
  } else {
    form.invoiceTitle = invoiceHeaderOptions.value[0]?.id;
  }
}

function close() {
  formRef.value?.resetFields();
  form.type = 1;
}
</script>

<style lang="scss" scoped>
.form {
  :deep(.el-radio-group) {
    display: flex;
    column-gap: 12px;

    .el-radio-button {
      .el-radio-button__inner {
        width: 160px;
        border: 1px solid #dcdfe6;
        border-radius: 4px;
      }

      &.is-active {
        .el-radio-button__inner {
          color: #0056ff;
          border: 1px solid #0056ff;
          border-left: 1px solid transparent;
          background-color: #fff;
        }

        &.is-disabled {
          .el-radio-button__inner {
            border-left: 1px solid #0056ff;
          }
        }
      }

      &:first-child {
        &.is-active {
          .el-radio-button__inner {
            color: #0056ff;
            border-left: 1px solid #0056ff;
            background-color: #fff;
          }
        }
      }
    }
  }

  .tip {
    color: #646b7a;
  }
}
</style>
