import http from "@assets/http";
import { serviceName } from "@assets/common";
import {arrayHasValue} from "jz-tool-lib";

// 设置ec线路
export const enableEcLineApi = {
  getData: (params: Record<string, any>) => {
    return http.get(`${serviceName.agentSystem}/user/ecSwitch`, {
      params: params,
    });
  },
};

// ec线路信息
export const ecLineInfoApi = {
  getData: (params: Record<string, any>) => {
    return http.get(`${serviceName.agentSystem}/company-ec/showEc`, {
      params,
    });
  },
  handleData: (data: Record<string, any>) => {
    return {
      info: data?.companyEc ?? {},
      userList: data?.userList ?? [],
    };
  },
};

// 配置ec线路
export const configureEcLineApi = {
  getData: (params: Record<string, any>) => {
    return http.post(`${serviceName.agentSystem}/company-ec/saveEc`, params);
  },
};
//ec线路保存
export const ecLineApi = {
  getData: (params: Record<string, any>) => {
    return http.post(`${serviceName.agentSystem}/company-ec/save`, params);
  },
};
//ec坐席删除
export const seatDeleteApi = {
  getData: (params: Record<string, any>) => {
    return http.post(`${serviceName.agentSystem}/company-ec/seat/delete`, params);
  },
};
//ec坐席新增编辑
export const seatEditApi = {
  getData: (params: Record<string, any>) => {
    return http.post(`${serviceName.agentSystem}/company-ec/seat/edit`, params);
  },
};
//套餐配置列表
export const PackageConfigurationListApi = {
  getData: (params) => {
    return http.get(
        `${serviceName.agentSystem}/customerResource/pageList`,
        { params: params }
    );
  },
  handleData: function (data) {
    const statusList = {
      1: "待开始",
      2: "进行中",
      3: "已结束",
      4: "已作废",
    };
    return {
      list: arrayHasValue(data?.records)
          ? data.records.map((item) => {
            const packageCycle = item.resourceStartTime && item.resourceEndTime
                ? `${item.resourceStartTime} - ${item.resourceEndTime}`
                : null;
            return{
              insideConsumptionResource:item.insideConsumptionResource,
              outsideConsumptionResource:item.outsideConsumptionResource,
              outsideDeductedAmount:item.outsideDeductedAmount,
              packageId:item.packageId,
              resourceEndTime:item.resourceEndTime,
              resourceStartTime:item.resourceStartTime,
              statusName: statusList[item.status] || "未知状态",
              status: item.status,
              totalResource:item.totalResource,
              id:item.id,
              packageCycle:packageCycle
            }
          })
          : [],
      total: parseInt(data.total),
    };
  },
};
//配置套餐
export const addResourceApi = {
  getData: (params: Record<string, any>) => {
    return http.post(`${serviceName.agentSystem}/customerResource/add`, params);
  },
};
//作废
export const packageCancellationApi = {
  getData: (params: Record<string, any>) => {
    return http.post(`${serviceName.agentSystem}/customerResource/resourceObsolete`, params);
  },
};
//套餐查看
export const packageQueryDetailsApi = {
  getData: (params) => {
    return http.get(
        `${serviceName.agentSystem}/packageSetting/queryDetails`,
        { params: params }
    );
  },
  handleData: function (data) {
    return {
      list: arrayHasValue(data?.records)
          ? data.records.map((item) => {
            return item
          })
          : [],
    };
  },
};
//查询可用套餐
export const availablePackagesApi = {
  getData: (params) => {
    return http.get(
        `${serviceName.agentSystem}/packageSetting/getAvailablePackageInfo`,
        { params: params }
    );
  },
  handleData: function (data) {
    return {
      list: arrayHasValue(data?.records)
          ? data.records.map((item) => {
            return item
          })
          : [],
      total: parseInt(data.total),
    };
  },
};
//获取以获取事件段
export const getOccupiedTimeSlotsApi = {
  getData(params: { companyId: any; agencyId: any }) {
    return http.get(
        `${serviceName.agentSystem}/customerResource/getOccupiedTimeSlots`,
        {
          params,
        }
    );
  },
  handleData(data) {
    return arrayHasValue(data)
        ? data.map((item) => {
          return {
            ...item,
          };
        })
        : [];
  },
};
