import http from "@assets/http";
import { contentType, serviceName } from "@assets/common";

// 充值---支付调起微信，支付宝二维码
interface paymentQRCodeInterface {
  paymentType: number | string;
  realPrice: number | string;
  rechargeImg?: string;
}
export const paymentQRCodeApi = {
  getData: (params: paymentQRCodeInterface) => {
    return http.get(`${serviceName.agentSystem}/order/getPayQrcodeByPrice`, {
      params: params,
    });
  },
};
// 确认微信，支付宝支付状态
export const confirmPaymentApi = {
  getData: (params: { orderNo: string }) => {
    return http.get(
      `${serviceName.agentSystem}/order/confirmPaidStatus/${params.orderNo}`
    );
  },
};

// 购买--支付调起微信，支付宝二维码
export const buyPaymentQRCodeApi = {
  getData: (params) => {
    return http.post(
      `${serviceName.agentSystem}/order/saveOrderAndRedirectPay`,
      params
    );
  },
};
// 服务市场购买支付
export const toPayApi = {
  getData: (params) => {
    return http.post(
      `${serviceName.marketVip}/serviceMarket/shopBuy/toPay`,
      params,
      contentType("yes")
    );
  },
};
// 更换购买支付
export const updatePayTypeApi = {
  getData: (params: { orderId: string; payType: string }) => {
    return http.post(
      `${serviceName.marketVip}/salesorder/updateOrderPaymethod?orderNo=${params.orderId}&payType=${params.payType}`
    );
  },
};
