<template>
  <el-dialog
    :model-value="visible"
    class="custom-dialog"
    width="550px"
    title="新增子客户"
    :before-close="handleClose"
  >
    <div class="body-container">
      <el-form
        ref="formRef"
        :model="formData"
        :rules="rules"
        label-width="110px"
      >
        <el-form-item label="客户类型">数企接口</el-form-item>
        <el-form-item
          prop="companyName"
          label="企业名称"
          class="remote-company"
        >
          <el-input
            v-model.trim="formData.companyName"
            placeholder="请输入企业名称"
            show-word-limit
            maxlength="28"
          ></el-input>
        </el-form-item>
        <el-form-item prop="industryId" label="所属行业">
          <CustomIndustryCascade
            v-model="formData.industryId"
            :emitPath="true"
          ></CustomIndustryCascade>
        </el-form-item>
        <el-form-item prop="companyPhone" label="公司联系手机">
          <el-input
            v-model="formData.companyPhone"
            placeholder="请输入公司联系手机"
          ></el-input>
        </el-form-item>
      </el-form>
    </div>
    <template #footer>
      <div class="dialog-footer">
        <el-button @click="handleClose">取消</el-button>
        <el-button type="primary" @click="handleSubmit">确定</el-button>
      </div>
    </template>
  </el-dialog>
</template>

<script lang="ts" setup>
import { ref, shallowReactive, defineAsyncComponent } from "vue";
import { ElMessage } from "element-plus";
import { addSubCustomerApi } from "@api/customer-management-api";

import { useModel } from "@assets/useHook";
import { checkoutTelephone } from "@assets/validatorHook";
//所属行业
const CustomIndustryCascade = defineAsyncComponent(
  () => import("@components/common/custom-industry-cascade.vue")
);
const props = defineProps<{
  visible: boolean;
}>();
const emits = defineEmits<{
  (e: "emitFunc");
  (e: "update:visible", value: boolean);
}>();

const formRef = ref();
const visibleModel = useModel(props, "visible", emits);
const formData = shallowReactive({
  companyName: "",
  industryId: "",
  companyPhone: "",
});
const rules = {
  companyName: [
    {
      required: true,
      message: "请输入公司名称",
      trigger: "blur",
    },
  ],
  industryId: [
    { required: true, message: "请选择所属行业", trigger: "change" },
  ],
  companyPhone: [
    {
      required: true,
      validator: (rule, value, callback) => {
        checkoutTelephone(rule, value, callback);
      },
      trigger: "blur",
    },
  ],
};
const handleSubmit = () => {
  if (!formRef.value) return;
  formRef.value.validate(async (valid) => {
    if (valid) {
      const params = {
        companyName: formData.companyName,
        industryId: formData.industryId.toString(),
        companyPhone: formData.companyPhone,
      };
      await addSubCustomerApi.getData(params);
      ElMessage.success("新增成功");
      emits("emitFunc");
      handleClose();
    } else {
      return false;
    }
  });
};
const handleClose = () => {
  formRef.value.resetFields();
  visibleModel.value = false;
};
</script>

<style lang="scss" scoped>
.body-container {
  padding: 0 10px;
}

:deep(.el-form-item) {
  .el-cascader,
  .el-select {
    width: 100%;
  }
}

.custom-upload-img {
  margin-bottom: 10px;
}
</style>
