/*
 * 补充资料记录
 * */
export const supplementLogComponentListApi = {
  getData: (params: Record<string, any>): Promise<any> => {
    return http.get(
      `${serviceName.callRest}/report-info-full-record/queryRecordByAdmin`,
      { params }
    );
  },
  statusLabelMap: {
    "2": "待审核",
    "3": "审核驳回",
    "4": "审核通过",
  },
  tagTypeMap: {
    "2": "warning",
    "3": "danger",
    "4": "success",
  },
  handleData: function (data, industryList) {
    const list = arrayHasValue(data) ? data : [];
    const componentList = list.map((item) => ({
      ...item,
      status: item.status,
      createdName: item.createdName,
      statusDesc: this.statusLabelMap[item.status],
      statusType: this.tagTypeMap[item.status],
      createdTime: dayjs(item.createdTime).format("YYYY-MM-DD HH:mm:ss"),
    }));
    componentList.map((e: Record<string, any>) => {
      e.recordContentList.map((item: any) => {
        // 多选框
        if (item.componentBO.propertyType === 5) {
          const ids = item.value
            ? item.value.split(",").map((v) => Number(v))
            : [];
          let str = "";
          item.componentBO.optionBOList.map((v) => {
            ids.includes(v.id) && (str += v.value + "；");
          });
          item.label = str;
          // 行业处理
        } else if (item.componentBO.propertyType === 10) {
          if (item.componentBO.propertyKey === "industry") {
            if (item.value) {
              item.value = getIndustryLabel(
                { children: industryList },
                item.value,
                0
              )
                .map((item: Record<string, any>) => item.label)
                .join("/");
            } else {
              item.value = "";
            }
          } else {
            item.value = "";
          }
          // 多选输入处理
        } else if (item.componentBO.propertyType === 12) {
          const valueArr = item.value ? JSON.parse(item.value) : [];
          let str = "";
          item.componentBO.optionBOList.map((v) => {
            const findItem = valueArr.find((x) => x.key == v.id);
            findItem && (str += `${v.value}：${findItem.value}；`);
          });
          item.label = str;
          // 单选、单选输入处理
        } else if (
          item.componentBO.propertyType === 4 ||
          item.componentBO.propertyType === 11
        ) {
          item.label =
            item.componentBO.optionBOList.find((v) => v.id == item.value)
              ?.value || "";
          // 图片
        } else if (item.componentBO.propertyType === 13) {
          item.value = item.fileUrl
            ? item.fileUrl
                .split(",")
                .map((v) => ({ url: v, type: "image", fileId: item.value }))
            : [];
          // 附件
        } else if (item.componentBO.propertyType === 9) {
          const urls = item.fileUrl ? item.fileUrl.split(",") : [];
          const fileIds = item.value ? item.value.split(",") : [];
          const fileNames = item.fileUrl
            ? item.fileUrl.split(",").map((v) => {
                const lineSplitArr = v.split("/");
                const len = lineSplitArr.length;
                if (len > 0) {
                  return lineSplitArr[len - 1];
                }
                return "";
              })
            : [];
          const fileTypes = fileNames.map((v) =>
            v.split(".").length ? v.split(".")[v.split(".").length - 1] : ""
          );
          item.value = urls.map((v, i) => ({
            url: v,
            fileId: fileIds[i],
            name: fileNames[i],
            type: ["jpg", "png", "jpeg", "JPG", "PNG", "JPEG"].includes(
              fileTypes[i]
            )
              ? "image"
              : "file",
          }));
        } else if (item.componentBO.propertyType === 7) {
          // 数字类型处理
          item.newValue = item.value ? Number(item.value) : item.value;
        }
        return item.componentBO;
      });
    });
    return {
      list: componentList,
    };
  },
};
/*
 * 业务报备动态组件（手动新增）
 * */
export const addReportComponentListApi = {
  getData: (params: Record<string, any>): Promise<any> => {
    return http.get(
      // `${serviceName.callRest}/reported/companyCallPhoneAgencyCodeApply/whenSaveQueryCompanyReportedComponent`,
      `${serviceName.callRest}/reported/lineReportedConfig/queryLineReportedConfigByLineIds`,
      { params }
    );
  },
  handleData: (data, companyInfo) => {
    data = arrayHasValue(data) ? data : [];
    const componentList = data
      .map((item: Record<string, any>) => item.componentBO)
      .filter((item: Record<string, any>) => item.propertyKey !== "key_words");
    componentList.map((item: any) => {
      if (item.propertyType === 1) {
        if (item.propertyKey === "company_name") {
          item.inputDisabled = true;
          item.value = companyInfo.companyName;
        } else if (item.propertyKey === "business_label") {
          item.value = [];
          item.labelText = [];
        } else {
          item.value = "";
        }
      } else if (item.propertyType === 7) {
        if (item.defaultValue) {
          item.value = Number(item.defaultValue);
        }
      } else if ([5, 12].includes(item.propertyType)) {
        item.value = [];
        item.optionBOList.forEach((v: Record<string, any>) => {
          v.value2 = "";
        });
      } else if ([9, 13].includes(item.propertyType)) {
        item.value = "";
        item.fileList = [];
        const verifyFileInfo = item.verifyBOList.find(
          (verifyItem) => verifyItem.type === "file"
        );
        item.fileLimit = verifyFileInfo?.length || 1;
      } else if (item.propertyType === 10) {
        if (item.propertyKey === "industry") {
          item.inputDisabled = true;
          item.value = companyInfo.industryId
            ? companyInfo.industryId.split(",")
            : [];
        } else {
          item.value = "";
        }
      } else {
        item.value = "";
      }
      return item;
    });
    return {
      list: componentList,
    };
  },
};
/*
 * 业务报备动态组件（手动编辑）
 * */
export const editReportComponentListApi = {
  getData: (params: Record<string, any>): Promise<any> => {
    return http.get(
      // `${serviceName.callRest}/reported/companyCallPhoneAgencyCodeApply/whenEditQueryCompanyReportedComponent`,
      `${serviceName.callRest}/reported/companyCallPhoneAgencyCodeApply/againSubmit`,
      { params }
    );
  },
  handleData: (data, companyInfo, industryInfo) => {
    const componentList = data
      .filter(
        (item: Record<string, any>) =>
          item.componentBO.propertyKey !== "key_words"
      )
      .map((item: Record<string, any>) => {
        if (item.componentBO.propertyType === 1) {
          if (item.componentBO.propertyKey === "company_name") {
            item.componentBO.inputDisabled = true;
            item.componentBO.value = companyInfo.companyName;
          } else if (item.componentBO.propertyKey === "business_label") {
            const groupList = item.value ? JSON.parse(item.value) : [];
            if (arrayHasValue(groupList)) {
              item.componentBO.value = groupList;
              item.componentBO.labelText = groupList.map((groupItem) => {
                const groupStr = groupItem?.group.labelGroupName;
                const labelStr = groupItem.labels
                  .map((labelItem) => {
                    return labelItem.labelName;
                  })
                  .toString();
                return `业务分类：${groupStr}；标签：${labelStr}`;
              });
            } else {
              item.componentBO.value = [];
              item.componentBO.labeText = [];
            }
          } else {
            item.componentBO.value = item.value;
          }
        } else if (item.componentBO.propertyType === 4) {
          // 单选处理
          const list = item.componentBO.optionBOList.map(
            (v: Record<string, any>) => v.id
          );
          if (item.value && list.includes(Number(item.value))) {
            item.componentBO.value = Number(item.value);
          } else {
            item.componentBO.value = "";
          }
        } else if (item.componentBO.propertyType === 5) {
          // 多选框处理
          if (item.value) {
            const arr = item.value.split(",");
            item.componentBO.value = arr.map((v: Record<string, any>) =>
              Number(v)
            );
          } else {
            item.componentBO.value = [];
          }
        } else if (item.componentBO.propertyType === 7) {
          // 数字类型处理
          item.componentBO.value = Number(item.value);
        } else if ([9, 13].includes(item.componentBO.propertyType)) {
          const verifyFileInfo = item.componentBO.verifyBOList.find(
            (verifyItem) => verifyItem.type === "file"
          );
          item.componentBO.fileLimit = verifyFileInfo?.length || 1;
          // 附件图片处理
          if (item.fileUrl) {
            const url = item.fileUrl.split(",");
            const key = item.value.split(",");
            const result: any = [];
            for (const i in url) {
              result.push({
                name: key[i],
                fileId: key[i],
                url: url[i],
              });
            }
            item.componentBO.fileList = result;
            item.componentBO.value = item.value;
          } else {
            item.componentBO.fileList = [];
            item.componentBO.value = "";
          }
        } else if (item.componentBO.propertyType === 10) {
          if (item.componentBO.propertyKey === "industry") {
            // 行业处理
            if (item.value) {
              item.componentBO.inputDisabled = true;
              item.componentBO.value = getIndustryLabel(
                { children: industryInfo },
                item.value,
                0
              ).map((v) => v.value);
            } else {
              item.componentBO.value = [];
            }
          } else {
            item.componentBO.value = item.value;
          }
        } else if (item.componentBO.propertyType === 11) {
          // 单选输入
          item.componentBO.value = item.value ? Number(item.value) : "";
        } else if (item.componentBO.propertyType === 12) {
          // 多选输入处理
          if (item.value) {
            const list = JSON.parse(item.value);
            item.componentBO.value = list.map((v: Record<string, any>) =>
              Number(v.key)
            );
            item.componentBO.optionBOList.map((v: Record<string, any>) => {
              list.map((k: any) => {
                if (v.id == k.key) {
                  v.value2 = k.value;
                }
              });
            });
          } else {
            item.componentBO.value = [];
          }
        } else {
          item.componentBO.value = item.value;
        }
        return item.componentBO;
      });
    return {
      list: componentList,
    };
  },
};
/*
 * 查看业务报备动态组件（手动详情）
 * */
export const detailReportComponentListApi = {
  getData: (params): Promise<any> => {
    return http.get(
      // `${serviceName.callRest}/reported/companyCallPhoneAgencyCodeApply/whenEditQueryCompanyReportedComponent`,
      `${serviceName.callRest}/reported/companyCallPhoneAgencyCodeApply/againSubmit`,
      { params }
    );
  },
  handleData: (data, industryList, speechLibraryList) => {
    const list = data.filter(
      (item: Record<string, any>) =>
        item.componentBO.propertyKey !== "key_words"
    );
    const componentList = list.map((item: Record<string, any>) => {
      if (item.componentBO.propertyType === 1) {
        if (item.componentBO.propertyKey === "business_label") {
          const groupList = item.value ? JSON.parse(item.value) : [];
          if (arrayHasValue(groupList)) {
            const groupList = JSON.parse(item.value);
            item.componentBO.labelText = groupList.map((groupItem) => {
              const groupStr = groupItem?.group.labelGroupName;
              const labelStr = groupItem?.labels
                .map((labelItem) => {
                  return labelItem.labelName;
                })
                .toString();
              return `业务分类：${groupStr}；标签：${labelStr}`;
            });
          } else {
            item.componentBO.labelText = ["暂无"];
          }
        } else {
          item.componentBO.value = item.value || "暂无";
        }
      } else if (item.componentBO.propertyType === 2) {
        if (item.componentBO.propertyKey === "verbal_trick") {
          if (item.value) {
            const speechInfo = speechLibraryList.find(
              (speechItem) => speechItem.id === item.value
            );
            item.componentBO.value = speechInfo?.title;
          } else {
            item.componentBO.value = "暂无";
          }
        } else {
          item.componentBO.value = item.value || "暂无";
        }
      } else if (item.componentBO.propertyType === 4) {
        // 单选处理
        const list = item.componentBO.optionBOList.map(
          (v: Record<string, any>) => v.id
        );
        if (item.value && list.includes(Number(item.value))) {
          item.componentBO.value = item.componentBO.optionBOList.find(
            (v: Record<string, any>) => v.id == item.value
          ).value;
        } else {
          item.componentBO.value = "";
        }
      } else if (item.componentBO.propertyType === 5) {
        // 多选框处理
        if (item.value) {
          const arr = item.value
            .split(",")
            .map((v: Record<string, any>) => Number(v));
          item.componentBO.value = item.componentBO.optionBOList
            .filter((v: Record<string, any>) => {
              return arr.includes(v.id);
            })
            .map((v: Record<string, any>) => v.value)
            .join("、");
        } else {
          item.componentBO.value = "";
        }
      } else if (item.componentBO.propertyType === 7) {
        // 数字类型处理
        item.componentBO.value = Number(item.value);
      } else if ([9, 13].includes(item.componentBO.propertyType)) {
        // 附件图片处理
        if (item.fileUrl) {
          const result: any[] = [];
          const url = item.fileUrl.split(",");
          const key = item.value.split(",");
          for (const i in url) {
            result.push({ name: key[i], fileId: key[i], url: url[i] });
          }
          item.componentBO.fileList = result;
          item.componentBO.value = item.value;
        } else {
          item.componentBO.fileList = [];
          item.componentBO.value = "";
        }
      } else if (item.componentBO.propertyType === 10) {
        // 行业处理
        if (item.componentBO.propertyKey === "industry") {
          if (item.value) {
            item.componentBO.value = getIndustryLabel(
              { children: industryList },
              item.value,
              0
            )
              .map((item: Record<string, any>) => item.label)
              .join("/");
          } else {
            item.componentBO.value = "";
          }
        } else {
          item.componentBO.value = "";
        }
      } else if (item.componentBO.propertyType === 11) {
        // 单选输入
        const list = item.componentBO.optionBOList.map(
          (v: Record<string, any>) => v.id
        );
        if (item.value && list.includes(Number(item.value))) {
          item.componentBO.value = item.componentBO.optionBOList.find(
            (v: Record<string, any>) => v.id == item.value
          ).value;
        } else {
          item.componentBO.value = "";
        }
      } else if (item.componentBO.propertyType === 12) {
        // 多选输入处理
        if (item.value) {
          const list = JSON.parse(item.value);
          item.componentBO.optionBOList.map((v: Record<string, any>) => {
            list.map((k: Record<string, any>) => {
              if (v.id == k.key) {
                v.value2 = k.value;
              }
            });
          });
          item.componentBO.value = item.componentBO.optionBOList
            .filter((v: Record<string, any>) => v.value2)
            .map((k: Record<string, any>) => {
              return { name: k.value, value: k.value2 };
            });
        } else {
          item.componentBO.value = "暂无";
        }
      } else if (item.componentBO.propertyType === 14) {
        // 话术关键词
        if (item.value) {
          item.componentBO.value = item.value.split(",");
        } else {
          item.componentBO.value = "暂无";
        }
      } else {
        item.componentBO.value = item.value || "暂无";
      }
      return item.componentBO;
    });
    return {
      list: componentList,
    };
  },
};
