/*
 * 业务报备详情（电子化）
 * */
export const electronReportingDetailApi = {
  getData: (params: { id: string; companyId: string }) => {
    return http.get(
      `${serviceName.callRest}/reported/companyCallPhoneAgencyCodeApply/getReportedComponentDetail`,
      {
        params,
      }
    );
  },
  handleData: (data, industryList) => {
    const { baseInfos, contractFileUrl, contractProveFileId, otherInfos } =
      data;
    const industryName = getIndustryLabel(
      { children: industryList },
      baseInfos.industry,
      0
    )
      .map((item: Record<string, any>) => item.label)
      .join("/");
    const baseInfo = {
      // 企业信息
      enterpriseInfo: {
        companyName: baseInfos.company_name,
        companyPhone: baseInfos.company_phone,
        legalName: baseInfos.legal_name,
        businessLicenseCode: baseInfos.business_license_code,
        industry: industryName,
      },
      // 法人信息
      legalPersonInfo: {
        legalName: baseInfos.legal_name,
        legalCard: baseInfos.legal_card,
        legalCardCode: baseInfos.legal_card_code,
        legalPhone: baseInfos.legal_phone,
      },
      // 经办人信息
      operatorInfo: {
        operatorName: baseInfos.operator_name,
        operatorCard: baseInfos.operator_card,
        operatorCardCode: baseInfos.operator_card_code,
        operatorPhone: baseInfos.operator_phone,
      },
      // 营业执照
      businessLicense: baseInfos.business_license,
    };
    // 授权承诺书文件
    const empowerPromiseLink = contractFileUrl;
    // 存证报告
    const stubReportFileId = contractProveFileId;
    // 其他信息
    const infoList = arrayHasValue(otherInfos)
      ? otherInfos.filter((item) => item.value)
      : [];
    infoList.map((item: Record<string, any>) => {
      if (item.componentBO.propertyType === 1) {
        if (item.componentBO.propertyKey === "business_label") {
          if (item.value) {
            const groupList = JSON.parse(item.value);
            item.componentBO.value = groupList.map((groupItem) => {
              const groupStr = groupItem?.group.labelGroupName;
              const labelStr = groupItem.labels
                .map((labelItem) => {
                  return labelItem.labelName;
                })
                .toString();
              return `业务分类：${groupStr}；标签：${labelStr}`;
            });
          } else {
            item.componentBO.value = ["暂无"];
          }
        } else {
          item.componentBO.value = item.value || "暂无";
        }
      } else if (item.componentBO.propertyType === 2) {
        if (item.componentBO.propertyKey === "verbal_trick") {
          item.componentBO.value = item.optionInputValue
            ? JSON.parse(item.optionInputValue).content
            : "";
        } else {
          item.componentBO.value = item.value;
        }
      } else if (item.componentBO.propertyType === 4) {
        // 单选处理
        item.componentBO.value = item.componentBO.optionBOList.find(
          (v: Record<string, any>) => v.id === Number(item.value)
        ).value;
      } else if (item.componentBO.propertyType === 5) {
        // 多选框处理
        const list = item.value
          .split(",")
          .map((v: Record<string, any>) => Number(v));
        item.componentBO.value = item.componentBO.optionBOList
          .filter((v: Record<string, any>) => {
            return list.includes(v.id);
          })
          .map((v: Record<string, any>) => v.value)
          .join("、");
      } else if (item.componentBO.propertyType === 7) {
        // 数字类型处理
        item.componentBO.value = Number(item.value);
      } else if (item.componentBO.propertyType === 10) {
        // 行业处理
        if (item.componentBO.propertyKey === "industry") {
          item.componentBO.value = getIndustryLabel(
            { children: industryList },
            item.value,
            0
          )
            .map((item: any) => item.label)
            .join("/");
        }
      } else if (item.componentBO.propertyType === 11) {
        // 单选输入
        item.componentBO.value = item.componentBO.optionBOList.find(
          (v: Record<string, any>) => v.id === Number(item.value)
        ).value;
      } else if (item.componentBO.propertyType === 12) {
        // 多选输入处理
        const list = JSON.parse(item.value);
        item.componentBO.optionBOList.map((v: Record<string, any>) => {
          list.map((k: Record<string, any>) => {
            if (v.id == k.key) {
              v.value2 = k.value;
            }
          });
        });
        item.componentBO.value = "";
        const arr = item.componentBO.optionBOList.filter((v: any) => v.value2);
        arr.forEach((k: any) => {
          item.componentBO.value += `${k.value}:${k.value2}；`;
        });
      } else if ([9, 13].includes(item.componentBO.propertyType)) {
        // 附件图片处理
        item.componentBO.value = item.fileUrl.split(",");
      } else if (item.componentBO.propertyType === 14) {
        // 话术关键词
        item.componentBO.value = item.value.split(",");
      } else {
        item.componentBO.value = item.value;
      }
      return item.componentBO;
    });
    const otherInfo = {
      normalList: infoList.filter((item) => {
        if (judgeDataType(item.componentBO.value) === "[object Array]") {
          item.componentBO.value = item.componentBO.value.toString();
        }
        return ![9, 13].includes(item.componentBO.propertyType);
      }),
      fileList: infoList.filter((item) => {
        if ([9, 13].includes(item.componentBO.propertyType)) {
          const data = item.componentBO.value;
          if (data) {
            if (judgeDataType(data) === "[object String]") {
              item.componentBO.value = [
                {
                  type: regExpExtensionName(data),
                  url: data,
                },
              ];
            } else if (arrayHasValue(data)) {
              item.componentBO.value = data.map((value) => {
                return {
                  type: regExpExtensionName(value),
                  url: value,
                };
              });
            }
          } else {
            item.componentBO.value = [];
          }
        }
        return [9, 13].includes(item.componentBO.propertyType);
      }),
    };
    return {
      baseInfo,
      stubReportFileId,
      empowerPromiseLink,
      otherInfo,
    };
  },
};
