//直客通话详情
export const directCallRecordListApi = {
  getData: (params): Promise<any> => {
    return http.post(`${serviceName.callRest}/call-record/list`, params);
  },
  handleData: function (data) {
    return {
      list: arrayHasValue(data?.records)
        ? data.records.map((item) => {
            const callStatusInfo = callStatusList.find(
              (serviceReportAudit) => serviceReportAudit.value === item.status
            );
            const callFlagInfo = callFlagList.find(
              (serviceReportAudit) => serviceReportAudit.value === item.callFlag
            );
            if (item.recordFileId) item.recordFlag = "有录音";
            else item.recordFlag = "无录音";
            return {
              calleeNum: item.calleeNum,
              callerNum: item.callerNum,
              callFlags: callFlagInfo?.label,
              callFlag: item.callFlag,
              companyId: item.companyId,
              recordFlag: item.recordFlag,
              recordFileId: item.recordFileId,
              recordFileUrl: item.recordFileUrl,
              disconnectTime: item.disconnectTime
                ? dayjs(item.disconnectTime).format("YYYY-MM-DD HH:mm:ss")
                : "-",
              connectTime: item.connectTime
                ? dayjs(item.connectTime).format("YYYY-MM-DD HH:mm:ss")
                : "-",
              fee: item.fee / 1000,
              status: item.status,
              callDetailsName: callStatusInfo?.label,
              timeConsume: item.timeConsume,
              callDetailsStyleType: callStatusInfo?.styleType,
            };
          })
        : [],
      total: data?.total,
    };
  },
};
