/*
 * 话术库列表
 * */
export const speechLibraryListApi = {
  getData: (params: Record<string, any>) => {
    return http.get(`${serviceName.callRest}/agentInterface/pageListCallText`, {
      params: params,
    });
  },
  handleData: (data) => {
    return {
      list: arrayHasValue(data.records)
        ? data.records.map((item) => {
            const speechLibraryInfo = arrayMatchValue(
              speechLibraryStatusList,
              item.status,
              "value"
            );
            item.groupId = item.groupId + "";
            item.statusName = speechLibraryInfo?.label;
            item.statusStyleType = speechLibraryInfo?.styleType;
            item.createdTime = item.createdTime
              ? dayjs(item.createdTime).format("YYYY-MM-DD HH:mm:ss")
              : "";
            item.list = arrayHasValue(item.list)
              ? item.list.map((operateItem) => {
                  const operateInfo = speechLibraryOperateTypeList.find(
                    (eventItem) => eventItem.value === operateItem.event
                  );
                  operateItem.remark = item.remark;
                  operateItem.operateLabel = operateInfo?.label;
                  operateItem.operateTime = operateItem.operateTime
                    ? dayjs(operateItem.operateTime).format(
                        "YYYY-MM-DD HH:mm:ss"
                      )
                    : "";
                  return operateItem;
                })
              : [];
            return item;
          })
        : [],
      total: data.total,
    };
  },
};
