// 流水账单
import http from "@assets/http";
import { serviceName } from "@assets/common";
import type { Response, List } from "@/api/financial-center/index.type";
import { arrayHasValue, toMoney } from "jz-tool-lib";

interface FlowBillList {
  consumeStartTime: string;
  consumeEndTime: string;
}

// 获取流水账单列表
export const queryFlowBillListApi = {
  getData: (params): Response<List<FlowBillList>> => {
    return http.get(`${serviceName.agentSystem}/bill/pageList`, { params });
  },
  handleData(data) {
    return {
      list: arrayHasValue(data.records)
        ? data?.records.map((item) => {
            return {
              id: item.id,
              // -1: 话费（分钟） -2 月租（个）
              productId: item.productId,
              tradeTime: dayjs(item.tradeTime).format("YYYY-MM-DD HH:mm:ss"),
              orderNo: item.orderNo,
              billType: item.billType,
              billTypeLabel: item.billTypeName,
              productName: item.productName,
              billMode: item.billMode,
              billModeLabel: item.billModeName,
              supervisorName: item.supervisorName,
              currentAccountName: item.currentAccountName,
              consumeTime: Array.of(
                item.consumeStartTime,
                item.consumeEndTime
              ).join(" - "),
              // 账单类型为2（退款）， 账单金额为负值
              billAmount:
                item.billType === 2
                  ? -toMoney(item.billAmount)
                  : toMoney(item.billAmount),
              commissionAmount: toMoney(item.commissionAmount),
              invoicableBalance: toMoney(item.invoicableBalance),
              balanceAmount: toMoney(item.balanceAmount),
              depositAmount: toMoney(item.depositAmount),
            };
          })
        : [],
      total: data?.total,
    };
  },
};

// 获取流水账单详情
export const queryFlowBillDetailApi = {
  billModeMap: {
    1: "预付款模式",
    2: "消耗扣费模式",
    3: "订单计费模式",
  },
  getData: (params): Response => {
    return http.get(`${serviceName.agentSystem}/bill/detail`, { params });
  },
  handleData(item) {
    return {
      id: item.id,
      productName: item.productName,
      billMode: item.billMode,
      billModeLabel: this.billModeMap[item.billMode],
      supervisorName: item.supervisorName,
      consumeTime: Array.of(
        dayjs(item.consumeStartTime).format("YYYY-MM-DD HH:mm:ss"),
        dayjs(item.consumeEndTime).format("YYYY-MM-DD HH:mm:ss")
      ).join(" - "),
      billAmount: item.billAmount.toFixed(2),
      commissionAmount: item.commissionAmount.toFixed(2),
      invoicableBalance: item.invoicableBalance.toFixed(2),
    };
  },
};

// 获取消耗明细
export const queryFlowBillConsumeDetailApi = {
  getData(params) {
    return http.get(`${serviceName.agentSystem}/bill/detailList`, { params });
  },
  handleData(arr) {
    return arr.map((item) => {
      return {
        id: item.id,
        productName: item.productName,
        quantity: item.quantity,
        price: toMoney(item.price),
        totalAmount: toMoney(item.totalAmount),
      };
    });
  },
};
