import http from "@assets/http";
import { serviceName, contentType } from "@assets/common";

// 登录
interface Login {
  userName: string;
  passWord: string;
  auto?: number;
  terminaltype?: string;
}

export const loginApi = {
  getData: (params: Login) => {
    return http.post(
      `${serviceName.bdsaas}/ajax/main/login.do`,
      params,
      contentType()
    );
  },
};

// 登录前校验账号是否锁定
export const loginVerifyApi = {
  getData: (userName: string) => {
    return http.get(
      `${serviceName.agentSystem}/accountStatus/check/${userName}`
    );
  },
};
// 登录后获取操作记录
interface LoginRecord {
  flag: boolean;
  userName: string;
}
export const loginRecordApi = {
  getData: (params: LoginRecord) => {
    return http.get(
      `${serviceName.agentSystem}/accountStatus/callback/${params.userName}/${params.flag}`
    );
  },
};
// 登录系统后更新用户信息
interface UpdateUserInfo {
  companyCode: string;
  companyId: string;
  mobile: string;
  profileId: string;
  userName: string;
}

export const updateUserInfoApi = {
  getData: (params: UpdateUserInfo) => {
    return http.post(
      `${serviceName.agentSystem}/user/updateUserInfoAfterLogin`,
      params
    );
  },
};

// 注册
interface Register {
  mobile: string;
  realName: string;
  password: string;
  rePassword: string;
  agencyCode: string;
  parentId: number;
}

export const registerApi = (params: Register) => {
  return http.post(
    `${serviceName.bdsaas}/ajax/agent/agentSystem/agentRegister.do`,
    params
  );
};

// 绑定代理商上下级关系
interface BindRelation {
  userName: string;
  userId: number;
  refererId: any;
  mobilePhone: string;
}

export const bindRelationApi = (params: BindRelation) => {
  return http.post(
    `${serviceName.agentSystem}/user/saveUserAfterRegister`,
    params
  );
};

// 获取图片验证码
export const imgCodeApi = () => {
  return `${serviceName.bdsaas}/ajax/main/getcodeimagemobile.do`;
};
