import http from "@assets/http";
import {
  customerOrderStatusList,
  customerOrderTypeList,
  dateTypeMap,
  saasPaymentTypeMap,
} from "@assets/typeStatus";
import { serviceName } from "@assets/common";
import { handleSpecsCode, indefiniteCodeList } from "@assets/productData";

import {
  arrayHasValue,
  arrayMatchValue,
  bToGB,
  judgeDataType,
} from "jz-tool-lib";

// 获取客户订单列表
interface CustomerOrderList {
  agentUserId: string;
  payStatus: number;
  orderNo: string;
  page: number;
  pageSize: number;
}

export const customerOrderListApi = {
  getData: (params: CustomerOrderList) => {
    return http.post(
      `${serviceName.marketAdmin}/agentSystem/salesorder/pageOrder`,
      params
    );
  },
  handleData: (data) => {
    return {
      list: arrayHasValue(data.records)
        ? data.records.map((item) => {
            const orderWayInfo = arrayMatchValue(
              customerOrderTypeList,
              {
                value: item.helpOrder,
              },
              "value"
            );
            const orderStatusInfo = arrayMatchValue(
              customerOrderStatusList,
              {
                value: item.orderstateCd,
              },
              "value"
            );
            let specsInfo = "";
            if (
              [
                "CLOUD_CALL_PACKAGE",
                "PACKAGE_CALL_SEAT",
                "CLOUD_CALL_PACKAGE_BIG",
                "PACKAGE_CALL_SEAT_BIG",
              ].includes(item?.combineServiceCode)
            ) {
              // 套餐、套餐坐席
              specsInfo += `坐席数量：${item.userNumber}个；合作时间：${
                item.dateValue
              }${dateTypeMap.get(item.dateType)}；`;
            } else if (
              ["CLOUD_CALL_SOFT", "CALL_SOFT_SEAT"].includes(
                item?.combineServiceCode
              )
            ) {
              // 软件、软件坐席
              specsInfo += `坐席数量：${item.userNumber}个；合作时间：${
                item.dateValue
              }${dateTypeMap.get(item.dateType)}；坐席单价：${
                item.seatUnitPrice
              }元；`;
            } else if (
              ["MARKETING_MESSAGE"].includes(item.combineServiceCode)
            ) {
              specsInfo = `服务周期：${item.dateValue}${dateTypeMap.get(
                item.dateType
              )}；`;
            }

            if (
              arrayHasValue(item.baseCodeList) &&
              !["CLOUD_CALL_SOFT", "CALL_SOFT_SEAT"].includes(
                item?.combineServiceCode
              )
            ) {
              item.baseCodeList.forEach((codeItem) => {
                const specData = handleSpecsCode(codeItem.baseCode);
                // 号码订购
                if (
                  ["CALL_TYPE", "LINE_TYPE", "NUMBER_TYPE"].includes(
                    codeItem.baseCode
                  )
                ) {
                  specsInfo += `${codeItem.name}：${
                    specData["value"] && specData["value"][codeItem.number]
                  }；`;
                } else if (
                  ["CLOUD_CALL_PACKAGE", "PACKAGE_CALL_SEAT"].includes(
                    item?.combineServiceCode
                  ) &&
                  ["MONTH_MINUTE"].includes(codeItem.baseCode)
                ) {
                  if (codeItem.number > 5000) {
                    specsInfo += `${codeItem.name}：不限制分钟数；`;
                  } else {
                    specsInfo += `${codeItem.name}：${codeItem.number}${specData.unit}；`;
                  }
                } else {
                  specsInfo += `${codeItem.name}：${codeItem.number}${specData.unit}；`;
                }
              });

              if (["CLOUD_CALL_PACKAGE"].includes(item?.combineServiceCode)) {
                specsInfo = specsInfo.replace(/呼叫套餐-/g, "");
              } else if (
                ["CALL_NUMBER_ORDER"].includes(item?.combineServiceCode)
              ) {
                // 号码订购-归属地
                const belong = !item.detailJson
                  ? ""
                  : JSON.parse(item.detailJson).reduce((pre, cur) => {
                      return (
                        pre +
                        `号码归属地：${cur.districtName}x${cur.number}个；`
                      );
                    }, "");
                specsInfo += belong;
              }
            }
            return {
              orderId: item.orderId,
              orderNo: item.orderNo,
              companyName: item.companyName,
              combineServiceName: item.combineServiceName,
              baseSpecsCode: specsInfo,
              createdTime: item.createdTime,
              helpOrder: item.helpOrder,
              helpOrderName: orderWayInfo?.label,
              total: item.total,
              couponPrice: item.couponPrice,
              actualPaid: item.actualPaid,
              orderStateCd: item.orderstateCd,
              orderStateCdName: orderStatusInfo?.label,
              orderStateCdStyleType: orderStatusInfo?.styleType,
              paymethodCd: item.paymethodCd,
              paymethodCdName: saasPaymentTypeMap.get(item.paymethodCd),
            };
          })
        : [],
      total: data.total ?? 0,
    };
  },
};

// 客户订单详情
export const customerOrderDetailApi = {
  getData: (orderId) => {
    return http.get(
      `${serviceName.marketAdmin}/agentSystem/salesorder/getOrderDetail?orderId=${orderId}`
    );
  },
  handleData: (data) => {
    // 订单详情
    const orderWayInfo = arrayMatchValue(
      customerOrderTypeList,
      {
        value: data?.helpOrder,
      },
      "value"
    );
    const orderStatusInfo = arrayMatchValue(
      customerOrderStatusList,
      {
        value: data?.orderstateCd,
      },
      "value"
    );

    // 订单信息
    const orderInfo = {
      orderId: data?.orderId,
      orderNo: data?.orderNo,
      voucherOrder: data?.voucherOrder,
      voucherPrice: data?.voucherPrice,
      companyId: data?.custRefId,
      profileId: data?.createdBy,
      companyName: data?.companyName,
      createdTime: data?.createdTime,
      helpOrder: data?.helpOrder,
      helpOrderName: orderWayInfo?.label,
      paymethodCd: data?.paymethodCd,
      orderStateCd: data?.orderstateCd,
      orderStateCdName: orderStatusInfo?.label,
      orderStateCdStyleType: orderStatusInfo?.styleType,
      total: new BigNumber(data?.total ?? 0).toFixed(2),
      couponPrice: new BigNumber(data?.couponPrice ?? 0).toFixed(2),
      actualPaid: new BigNumber(data?.actualPaid ?? 0).toFixed(2),
    };
    // 付款信息
    const realityPrice = new BigNumber(
      (data?.total ?? 0) - (data?.couponPrice ?? 0)
    ).toFixed(2);
    const paymentInfo = {
      total: new BigNumber(data?.total ?? 0).toFixed(2), // 订单金额
      couponPrice: new BigNumber(data?.couponPrice ?? 0).toFixed(2), // 优惠总金额
      undertakeDiscount: new BigNumber(data?.undertakeDiscount ?? 0).toFixed(2), // 承担优惠
      planCommission: new BigNumber(data?.planCommission ?? 0).toFixed(2), // 计划返佣
      actualCommission: new BigNumber(data?.actualCommission ?? 0).toFixed(2), // 实际返佣
      realityPrice: realityPrice, // 订单实际金额  = 订单金额 - 优惠总金额
      actualPaid: new BigNumber(data?.actualPaid ?? 0).toFixed(2), // 实际支付金额
    };

    // 服务周期
    const dateInfo = {
      type: data.dateType,
      value: data.dateValue,
    };
    // 商品列表
    const listInfo = arrayHasValue(data.list)
      ? data.list.map((item: Record<string, any>, index: number) => {
          let specsInfo = "";
          if (
            [
              "CLOUD_CALL_PACKAGE",
              "PACKAGE_CALL_SEAT",
              "CLOUD_CALL_PACKAGE_BIG",
              "PACKAGE_CALL_SEAT_BIG",
            ].includes(item?.combineServiceCode)
          ) {
            // 套餐、套餐坐席
            specsInfo += `坐席数量：${item.userNumber}个；合作时间：${
              dateInfo.value
            }${dateTypeMap.get(dateInfo.type)}；`;
          } else if (
            ["CLOUD_CALL_SOFT", "CALL_SOFT_SEAT"].includes(
              item?.combineServiceCode
            )
          ) {
            // 软件、软件坐席
            specsInfo += `坐席数量：${item.userNumber}个；合作时间：${
              dateInfo.value
            }${dateTypeMap.get(dateInfo.type)}；坐席单价：${
              item.seatUnitPrice
            }元；`;
          } else if (["MARKETING_MESSAGE"].includes(item.combineServiceCode)) {
            specsInfo = `服务周期：${dateInfo.value}${dateTypeMap.get(
              dateInfo.type
            )}；`;
          }

          if (
            arrayHasValue(item.baseCodeList) &&
            !["CLOUD_CALL_SOFT", "CALL_SOFT_SEAT"].includes(
              item?.combineServiceCode
            )
          ) {
            item.baseCodeList.forEach((codeItem) => {
              console.log(codeItem);
              const specData = handleSpecsCode(codeItem.baseCode);
              // 号码订购
              if (
                ["CALL_TYPE", "LINE_TYPE", "NUMBER_TYPE"].includes(
                  codeItem.baseCode
                )
              ) {
                specsInfo += `${codeItem.name}：${
                  specData["value"] && specData["value"][codeItem.number]
                }；`;
              } else if (
                ["CLOUD_CALL_PACKAGE", "PACKAGE_CALL_SEAT"].includes(
                  item?.combineServiceCode
                ) &&
                ["MONTH_MINUTE"].includes(codeItem.baseCode)
              ) {
                if (codeItem.number > 5000) {
                  specsInfo += `${codeItem.name}：不限制分钟数；`;
                } else {
                  specsInfo += `${codeItem.name}：${codeItem.number}${specData.unit}；`;
                }
              } else {
                specsInfo += `${codeItem.name}：${codeItem.number}${specData.unit}；`;
              }
            });

            if (["CLOUD_CALL_PACKAGE"].includes(item?.combineServiceCode)) {
              specsInfo = specsInfo.replace(/呼叫套餐-/g, "");
            } else if (
              ["CALL_NUMBER_ORDER"].includes(item?.combineServiceCode)
            ) {
              // 号码订购-归属地
              const belong = !data.detailJson
                ? ""
                : JSON.parse(data.detailJson).reduce((pre, cur) => {
                    return (
                      pre + `号码归属地：${cur.districtName}x${cur.number}个；`
                    );
                  }, "");
              specsInfo += belong;
            }
          }
          const augmentList = arrayHasValue(item.augments)
            ? customerOrderDetailApi.transformList(item.augments, dateInfo)
            : [];
          return {
            id: index,
            specsInfo,
            augmentList,
            combineServiceName: data?.combineServiceName,
            combineServiceCode: data?.combineServiceCode,
            actualPaid: new BigNumber(data?.actualPaid ?? 0).toFixed(2),
            isEditPrice: data?.helpOrder === 0 && data?.orderstateCd === "NP",
          };
        })
      : [];
    return {
      listInfo,
      orderInfo,
      paymentInfo,
    };
  },
  transformList: function (
    list: Record<string, any>[],
    dateInfo: {
      type: string;
      value: number;
    }
  ): unknown {
    if (arrayHasValue(list)) {
      return list.map((item: Record<string, any>) => {
        return {
          specsInfo: this.specsInfo(dateInfo),
          actualPaid: new BigNumber(item?.actualPaid).toFixed(2),
          total: new BigNumber((item?.total ?? 0) / 100).toFixed(2),
          imgUrl: item?.imgUrl,
          combineServiceName: item?.combineServiceName,
          combineServiceTitle: item?.combineServiceTitle,
        };
      });
    }
  },
  specsInfo: function (dateInfo: { type: string; value: number }) {
    return `服务周期：${dateInfo.value}${dateTypeMap.get(dateInfo.type)}`;
  },
};

