// 客户订单操作记录
export const operationRecordApi = {
  getData: (orderId: string) => {
    return http.get(
      `${serviceName.marketVip}/salesorder-log/getOrderLog?orderId=${orderId}`
    );
  },
  handleData: (data) => {
    const operateList = [
      { label: "提交订单", value: 0 },
      { label: "支付订单", value: 1 },
      { label: "修改订单", value: 2 },
      { label: "取消订单", value: 3 },
      { label: "订单退款", value: 4 },
    ];
    return arrayHasValue(data)
      ? data.map((item) => {
          const operateInfo = operateList.find(
            (operateItem) => operateItem.value === item.operType
          );
          const operateContent =
            item.operType === 2
              ? `订单价格由${item.total}元改为${item.actualTotal}元`
              : operateInfo?.label;
          return {
            operator: `${item.userName ?? ""}-${item.mobile ?? ""}`,
            personType: item.helpOrder === 0 ? "客户" : "代理商",
            operateType: operateInfo?.label,
            operateContent: operateContent,
            createdTime: dayjs(item.createdTime).format("YYYY-MM-DD HH:mm:ss"),
          };
        })
      : [];
  },
};

