// 获取服务产品信息
export const productDetailApi = {
  getData: (params) => {
    return http.post(
      `${serviceName.marketVip}/serviceMarket/combineService/queryCombineServiceByCode?serviceCode=${params.serviceCode}`
    );
  },
  handleData: (data) => {
    // 增值服务
    let addedServiceList: any[] = [];
    const addedServiceArr = data.addedServiceMarketBoList;
    if (arrayHasValue(addedServiceArr)) {
      addedServiceList = addedServiceArr.map((item: Record<string, any>) => {
        const specsList = item.serviceMarketSizeGroupList.filter(
          (specsItem: Record<string, any>) => specsItem.hidden === 0
        );
        const specsValueList = item.serviceMarketSizeValueList;
        return {
          specsList,
          specsValueList,
          isDiy: item.isDiy,
          miniNum: item.mininum,
          groupCodes: specsList[0]?.groupCodes,
          addedServiceName: item.addedServiceName,
          addedServiceCode: item.addedServiceCode,
        };
      });
    }
    const serviceInfo = {
      combineServiceName: data.combineServiceName,
      combineServiceTitle: data.combineServiceTitle,
      combineServiceCode: data.combineServiceCode,
      addedCodes: data.addedCodes,
      calculateType: data.calculateType,
      imgUrlStr: data.imgUrlStr,
      unit: handleSpecsCode(data.combineServiceCode).unit,
    };
    const combineServiceList = data.combineServiceUserNumberList ?? [];
    // calculateType 1：普通服务
    //               2：增值服务
    //               3：鱼鹰呼叫套餐
    //               4：服务人数
    //               5：呼叫套餐坐席，
    //               6：鱼鹰呼叫软件，鱼鹰呼叫软件坐席
    //               7：录音增值包服务
    //               9：事儿电话
    // paramType     1：权限类资源
    //               2：增值类资源
    return {
      serviceInfo,
      addedServiceList,
      combineServiceList,
    };
  },
};

