// 获取已购的增值服务信息
export const purchasedAddedProductApi = {
  getData: (params) => {
    return http.post(
      `${serviceName.marketVip}/consumption/queryCompanyResourceByCode?resourceCode=${params.resourceCode}&companyId=${params.companyId}`
    );
  },
  handleData: (data) => {
    return arrayHasValue(data)
      ? data.map((item: Record<string, any>) => {
          const resourceCount = purchasedAddedProductApi.resourceCount(item);
          return {
            ...item,
            resourceCount,
            endTime: indefiniteCodeList.includes(item.consumptionTypeCd)
              ? "永久有效"
              : dayjs(item.endTime).format("YYYY-MM-DD"),
          };
        })
      : [];
  },
  resourceCount: (item) => {
    let count: number;
    switch (item.consumptionTypeCd) {
      case "STORE":
        count = bToGB(new BigNumber(item.owned).minus(item.used).toNumber());
        break;
      case "HWCZ":
        count = Number(
          new BigNumber(item.owned).minus(item.used).dividedBy(1000).toFixed(2)
        );
        break;
      default:
        count = new BigNumber(item.owned).minus(item.used).toNumber();
    }
    return count;
  },
};

