// 获取产品购买价格
export const productPriceApi = {
  getData: (params) => {
    return http.post(
      `${serviceName.marketVip}/serviceMarket/shopBuy/getCombineServicePrice`,
      params
    );
  },
  handleData: (data) => {
    const list = arrayHasValue(data) ? data : [];
    const productList = list.map((item) => {
      // 附赠产品列表
      let addedPrice = 0,
        subMarketList = [];
      if (arrayHasValue(item.subMarketPrice)) {
        subMarketList = item.subMarketPrice.map((subItem) => {
          let specsInfo = "";
          if (judgeDataType(subItem.minNumMap) === "[object Object]") {
            const valueList = Object.values(subItem.minNumMap);
            specsInfo = valueList.toString();
          }
          addedPrice += subItem.price;
          return {
            specsInfo,
            price: new BigNumber(subItem.price).toFixed(2),
            showPrice: new BigNumber(subItem.showPrice).toFixed(2),
            imgUrlStr: subItem.imgUrl,
            calculateType: subItem.calculateType,
            combineServiceCode: subItem.marketCode,
            combineServiceName: subItem.marketName,
            combineServiceTitle: subItem.combineServiceTitle,
          };
        });
      }
      return {
        errorMsg: item.errorMsg,
        errorCode: item.errorCode,
        combineServiceName: item.marketName,
        combineServiceCode: item.marketCode,
        unitPrice: item.agentUnitPrice
          ? new BigNumber(item.agentUnitPrice).toFixed(2)
          : "0.00",
        price: item.agentPrice
          ? new BigNumber(item.agentPrice).toFixed(2)
          : "0.00", // 产品价格（不包括附赠产品）
        addedPrice: new BigNumber(addedPrice).toFixed(2), // 附赠产品价格
        sumPrice: item.agentBuyPrice
          ? new BigNumber(item.agentBuyPrice).toFixed(2)
          : "0.00", // 产品购买总价格（包括附赠产品）
        subMarketList,
      };
    });
    const sumPrice = productList.reduce(
      (total: number, item: Record<string, any>) => {
        // 产品购买总价格
        const sumPrice = new BigNumber(item.sumPrice).toNumber();
        return new BigNumber(total + sumPrice).toFixed(2);
      },
      0
    );
    return {
      sumPrice: new BigNumber(sumPrice).toFixed(2),
      productList,
    };
  },
};

// 获取续费基础价格
export const renewPriceApi = {
  getData: (params) => {
    return http.post(
      `${serviceName.marketVip}/serviceMarket/shopBuy/getRenewUnitPrice?companyId=${params.companyId}&marketCode=${params.marketCode}&addedCode=${params.addedCode}&agentId=${params.agentId}&helpOrder=${params.helpOrder}`
    );
  },
  handleData: (data) => {
    // 附赠产品列表
    let subMarketList = [];
    if (arrayHasValue(data.subMarketPrice)) {
      subMarketList = data.subMarketPrice.map((subItem) => {
        let specsInfo = "";
        if (judgeDataType(subItem.minNumMap) === "[object Object]") {
          const valueList = Object.values(subItem.minNumMap);
          specsInfo = valueList.toString();
        }
        return {
          specsInfo,
          price: new BigNumber(subItem.price).toFixed(2),
          showPrice: new BigNumber(subItem.showPrice).toFixed(2),
          imgUrlStr: subItem.imgUrl,
          calculateType: subItem.calculateType,
          combineServiceCode: subItem.marketCode,
          combineServiceName: subItem.marketName,
          combineServiceTitle: subItem.combineServiceTitle,
        };
      });
    }
    const addedPrice = arrayHasValue(data.subMarketPrice)
      ? data.subMarketPrice.reduce((total, item) => {
          return total + new BigNumber(item.price).toNumber();
        }, 0)
      : "0.00";
    return {
      seatUnitPrice: data.seatUnitPrice, // 坐席单价
      agentUnitPrice: data.agentUnitPrice, // 坐席代理单价
      basePrice: data.price, // 产品基础价格，比如1年单价，套餐或者软件每个月价格
      addedPrice: new BigNumber(addedPrice).toFixed(2), // 附赠产品总价格
      combineServiceName: data.marketName,
      combineServiceCode: data.marketCode,
      subMarketList,
    };
  },
};

// 获取更多代理商品
export const agentProductListApi = {
  getData: () => {
    return http.post(`${serviceName.agentSystem}/product/queryMarketProduct`);
  },
  handleData: (data, businessType) => {
    if (arrayHasValue(data)) {
      const list = data.filter((item) => {
        return agentProductListApi
          .serviceCodeList(businessType)
          .includes(item.combineServiceCode);
      });
      return list.map((item) => {
        return {
          imgUrlStr: item.imgUrlStr,
          calculateType: item.calculateType,
          combineServiceCode: item.combineServiceCode,
          combineServiceName: item.combineServiceName,
          combineServiceTitle: item.combineServiceTitle,
        };
      });
    } else {
      return [];
    }
  },
  serviceCodeList: (businessType: string) => {
    if (businessType === "1") {
      return ["CALL_NUMBER_ORDER"];
    }
    return [
      /****呼叫产品*****/
      "CLOUD_CALL_PACKAGE", // 呼叫套餐
      "PACKAGE_CALL_SEAT", // 套餐坐席
      "CLOUD_CALL_SOFT", // 呼叫软件
      "CALL_SOFT_SEAT", // 软件坐席
      "CLOUD_CALL_PACKAGE_BIG", // 呼叫套餐（大客户套餐）
      "PACKAGE_CALL_SEAT_BIG", // 呼叫坐席（大客户套餐）
      "VOICE_SERVICE", // 话费充值
      "CALL_NUMBER_ORDER", // 号码订购
      "MARKETING_MESSAGE", // 数企短信营销
      "MESSAGE_TYPE_SERVICE", // 短信条数
      /****云集客、云查客产品*****/
      "VIDEO_MATRIX", // 视频矩阵
      "VIDEO_MATRIX_ACCOUNT", // 视频矩阵账号
      "CLOUD_CUSTOMER_21S", //21S云集客（新）
      "BADOU_21S", //八豆
      "INTERFACES_21S", //接口数
      "ACCOUNT_ACCESS_21S", //账户数
      "EXPAND_CUSTOMER_PRO", //云查客企业版
      "JMB", //渠道商加盟包
      // "PRO_CUS_UNLOCHED_ADD", // 解锁额度增量包（先注释，后期可能要用）
      // "CUSTOMER_SERVICE", // 客户增量（先注释，后期可能要用）
      // "RECORD_ADDED_SERVICE", // 录音增值包（先注释，后期可能要用）
    ];
  },
};

