<template>
  <el-radio-group :model-value="props.modelValue" @change="handleChange">
    <el-radio
      v-for="(item, index) in props.list"
      :key="index"
      :value="item.value"
      :disabled="item.disabled"
      border
      >{{ item.label }}
      <el-tooltip
        v-if="tooltip"
        placement="top-start"
        trigger="hover"
        popper-class="custom-tooltip"
        :content="tooltip[item.value]"
      >
        <img
          class="question-icon"
          src="@/assets/images/icon-question.png"
          alt=""
      /></el-tooltip>
    </el-radio>
  </el-radio-group>
</template>

<script setup lang="ts">
const props = defineProps<{
  list: any[];
  minWidth?: string;
  modelValue: string;
  tooltip?: any;
}>();
const emits = defineEmits<{
  (e: "update:modelValue", value: any);
}>();

function handleChange(value) {
  emits("update:modelValue", value);
}
</script>

<style scoped lang="scss">
:deep(.el-radio.is-bordered) {
  padding: 0 20px;
  margin: 0 15px 15px 0;

  .el-radio__inner {
    display: none;
  }

  .el-radio__label {
    @include flex(center, center);
    min-width: v-bind(minWidth);
    padding-left: 0;
    text-align: center;
  }

  &:last-child {
    margin-right: 0;
  }
}

.question-icon {
  width: 16px;
  height: 16px;
  margin-left: 5px;
}
</style>
