<template>
  <!-- 认证审核中 -->
  <el-dialog
    class="custom-dialog"
    width="520px"
    lock-scroll
    append-to-body
    :close-on-click-modal="false"
    title="提示"
    :model-value="visible"
    @close="handleClose"
  >
    <!--  未认证  -->
    <div class="not-container container" v-if="certificationStatus === 0">
      <p class="word">未认证</p>
      <p class="prompt">当前没有认证服务商,请前去认证</p>
      <el-button
        type="primary"
        color="#0056ff"
        @click="goPage('/certificationService')"
        >认证
      </el-button>
    </div>
    <!--   认证审核中  -->
    <div class="waiting-container container" v-if="certificationStatus === 1">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-certification-waiting"></use>
      </svg>
      <p class="word">信息审核中</p>
      <p class="prompt">
        尊敬的服务商，您好！您的信息正在审核中，1~3个工作日将会审核完成，请您耐心等待！
      </p>
    </div>
    <!--  审核通过  -->
    <div class="pass-container container" v-if="certificationStatus === 2">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-certification-pass"></use>
      </svg>
      <p class="word">认证审核通过</p>
      <p class="prompt">
        恭喜您已通过系统认证审核，现在可以去服务市场购买代理产品了
      </p>
      <el-button type="primary" color="#0056ff" @click="buyProduct"
        >去购买
      </el-button>
    </div>
    <!--  审核驳回  -->
    <div class="refuse-container container" v-if="certificationStatus === 3">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#icon-certification-refuse"></use>
      </svg>
      <p class="word">审核驳回</p>
      <p class="prompt">驳回原因：{{ checkAdvice }}</p>
      <el-button
        type="primary"
        color="#0056ff"
        @click="goPage('/certificationService?checkStatus=0')"
        >重新认证
      </el-button>
    </div>
  </el-dialog>
</template>

<script lang="ts" setup>
import { useVueRouter } from "@assets/useHook";
import { updateMsgReadStateApi } from "@api/common-api";
defineProps<{
  visible: boolean;
  checkAdvice?: string;
  certificationStatus: number | string;
}>();
const emits = defineEmits<{
  (e: "update:visible", value: boolean): void;
}>();
const { goPage } = useVueRouter();
const handleClose = () => {
  updateMsgReadState().then(() => {
    emits("update:visible", false);
  });
};
const buyProduct = () => {
  updateMsgReadState().then(() => {
    goPage("/marketService");
    emits("update:visible", false);
  });
};
const updateMsgReadState = () => {
  return updateMsgReadStateApi.getData({ state: 1 });
};
</script>

<style lang="scss" scoped>
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 440px;
  min-height: 170px;
  margin: 0 auto;
  svg {
    width: 36px;
    height: 36px;
  }
  .word {
    font-size: 18px;
    color: #242934;
    line-height: 20px;
    margin: 12px 0 20px;
  }
  .prompt {
    font-size: 14px;
    color: #9ca6b9;
    line-height: 1;
    text-align: center;
  }
  .el-button {
    margin-top: 40px;
  }
}
</style>
