import { burglarproof } from "@assets/common";
import { LocalStorage, SessionStorage } from "jz-tool-lib";
import { ElLoading, ElNotification } from "element-plus";

const instance = axios.create({
  timeout: 60000,
  headers: {
    "Content-Type": "application/json;charset=utf-8",
  },
});
let loading: any = null;
instance.interceptors.request.use(
  function (request) {
    loading = ElLoading.service({
      background: "rgba(0, 0, 0, 0.25)",
    });
    return handleRequest(request);
  },
  function (error) {
    clearLoading();
    return Promise.reject(error);
  }
);
instance.interceptors.response.use(
  function (response) {
    clearLoading();
    return handleResponse(response);
  },
  function (error) {
    clearLoading();
    ElNotification({
      type: "error",
      title: "请求错误",
      duration: 3000,
      message: error,
    });
    return Promise.reject(error);
  }
);

// 清除loading实例
function clearLoading() {
  if (loading) loading.close();
  loading = null;
}

/*
 * 请求前处理
 * */
function handleRequest(request) {
  const burglarproofInfo = burglarproof();
  request.headers.xwsse = burglarproofInfo.XWsse;
  request.headers.timestamp = burglarproofInfo.timestamp;
  request.headers["Tracking-Url"] = window.location.href;
  request.headers.Authorization = `Bearer ${LocalStorage.getItem(
    "bd_authorization"
  )}`;
  if (request?.headers?.qsParams === "yes") {
    request.data = Qs.stringify(request.data);
  }
  return Promise.resolve(request);
}

/*
 * 响应成功处理
 * headers头里面自定义字段：Notification, ResponseFormat
 * Notification：字段用来标识要不要ElNotification提示。no：表示不需要。需要就不用在headers头设置 notification
 * ResponseFormat：设置响应类型标记。arraybuffer：表格文件流。默认不设置为：application/json
 * */
function handleResponse(response) {
  const { data: responseData, config: responseConfig } = response;
  if (!responseData) {
    ElNotification({
      type: "error",
      title: "提示",
      duration: 3000,
      message: "请求失败",
    });
    return Promise.reject(response);
  } else {
    if (["arraybuffer", "blob"].includes(responseConfig.responseType)) {
      return Promise.resolve(response);
    } else {
      if (![0, 200].includes(responseCode(responseData))) {
        ElNotification({
          type: "error",
          title: "提示",
          duration: 3000,
          message: responseDescribe(responseData) ?? "请求失败",
        });
        handleCode(responseCode(responseData));
        return Promise.reject(response);
      } else {
        return Promise.resolve(responseData);
      }
    }
  }
}

// 请求成功后，状态码处理
function handleCode(code) {
  switch (code) {
    case 80001:
      clearStorage();
      window.location.href = "/";
      return;
    case 401:
      clearStorage();
      window.location.href = "/";
      return;
    default:
      return;
  }
}

function clearStorage() {
  const loginForm = LocalStorage.getItem("bd_loginForm");
  LocalStorage.clear();
  SessionStorage.clear();
  loginForm && LocalStorage.setItem("bd_loginForm", loginForm);
}

/*
 * 判断每个服务返回的状态码和描述字段
 * 服务名称：bdsaas 状态码：statusCode，描述：rspMsg
 * 服务名称：agentsystem 状态码：statusCode，描述：msg
 * 服务名称：market-vip-rest 状态码：statusCode，描述：msg
 * */

// 处理响应状态code
function responseCode(responseData) {
  if ("rspCode" in responseData) {
    return responseData.rspCode;
  } else if ("statusCode" in responseData) {
    return responseData.statusCode;
  }
}

// 处理响应描述describe
function responseDescribe(responseData) {
  if ("rspMsg" in responseData) {
    return responseData.rspMsg;
  } else if ("msg" in responseData) {
    return responseData.msg;
  }
}

export default instance;
