import { testPassword, testTelephone, testIdentification } from "jz-tool-lib";
// 校验手机号
export const checkoutTelephone = (rule: any, value: string, callback: any) => {
  if (value === "") {
    callback(new Error("请输入手机号码"));
  } else {
    if (!testTelephone(value)) {
      callback(new Error("请输入正确的手机号码"));
    }
    callback();
  }
};
// 校验身份证
export const checkoutIdentification = (
  rule: any,
  value: string,
  callback: any
) => {
  if (value === "") {
    callback(new Error("请输入身份证号码"));
  } else {
    if (!testIdentification(value)) {
      callback(new Error("请输入正确的身份证号码"));
    }
    callback();
  }
};
// 校验密码
export const validatePassword = (rule, value, callback, ...args) => {
  const [ruleFormData, ruleFormRef, propName] = args;
  if (value === "") {
    callback(new Error("请输入密码"));
  } else {
    if (!testPassword(value)) {
      callback(
        new Error(
          "您的密码过于简单。密码设置要求8~16位，包含大写字母、小写字母、数字、特殊字符（.!@#$%^&*`~()-+=），任意3种组合；"
        )
      );
    } else {
      if (ruleFormData[propName] !== "") {
        ruleFormRef.value.validateField(propName);
      }
    }
    callback();
  }
};
// 重复密码校验
export const validateRePassword = (rule, value, callback, ...args) => {
  const [ruleFormData, propName] = args;
  if (value === "") {
    callback(new Error("请再次输入密码"));
  } else if (value !== ruleFormData[propName]) {
    callback(new Error("两次输入密码不一致!"));
  } else {
    callback();
  }
};
