<template>
  <el-dialog
    v-model="initData.visible"
    title="业务场景"
    width="1000px"
    align-center
    class="business-scenarios-dialog"
    :before-close="handleClose"
  >
    <el-alert
      type="warning"
      v-if="initData.showAlert"
      @close="initData.showAlert = false"
    >
      <template #default>
        <div class="alert-content">
          <p>
            提示：1、需要依据您公司所经营的业务范围据实选择，否则会导致您无法正常使用外呼功能，请知悉。
          </p>
          <p>
            2、购买码号时，只可选择同一个行业下的业务标签选购，不可选择多个行业，请知悉。
          </p>
        </div>
      </template>
    </el-alert>
    <div class="selected-tag">
      <p v-if="!selectMinorTagIdList.length">
        已选<strong>"{{ initData.selectPrevMainTag?.name }}"</strong
        >行业<strong>0</strong>个标签
      </p>
      <p v-if="selectMinorTagIdList.length">
        已选<strong>"{{ initData.selectPrevMainTag?.name }}"</strong>行业
        <strong>{{ initData.selectMinorTag.length }}</strong
        >个标签， 共<strong>{{ selectedTag.total }}</strong
        >条可用，包括：
        <span v-for="item in selectedTag.list"
          ><strong>{{ item.name }}</strong
          >:<strong>{{ item.value.length }}条</strong>；
        </span>
      </p>
    </div>
    <div class="business-content">
      <ul class="group-list">
        <li
          v-for="groupItem in initData.mainTagList"
          :key="groupItem.id"
          class="group-item"
          :id="groupItem.id"
        >
          <span
            @click="handleMainTag(groupItem)"
            :class="[
              'group-name',
              selectMainTagId === groupItem.id ? 'checked' : '',
            ]"
            >{{ groupItem.name }}</span
          >
        </li>
      </ul>
      <template v-if="initData.minorTagList.length">
        <ul class="label-list">
          <li
            v-for="item in initData.minorTagList"
            :key="item.id"
            :class="[
              'label-item',
              selectMinorTagIdList.includes(item.id) ? 'checked' : '',
            ]"
            @click="handleMinorTag(item)"
          >
            {{ item.name }}
          </li>
        </ul>
      </template>
      <template v-else>
        <TableEmpty empty-text="暂无标签~"></TableEmpty>
      </template>
    </div>
    <template #footer>
      <div class="dialog-footer">
        <el-button @click="handleClose">取消</el-button>
        <el-button
          type="primary"
          :disabled="!initData.selectMinorTag.length"
          @click="handleSubmit"
          >确定
        </el-button>
      </div>
    </template>
  </el-dialog>
</template>

<script setup lang="ts">
import { ElMessage } from "element-plus";
import { computed, nextTick, reactive } from "vue";

import {
  businessMainTagApi,
  businessMinorTagApi,
} from "@api/order-management-api";

const emits = defineEmits<{
  (
    e: "emitFunc",
    data: {
      list: Record<string, any>[];
      label: string[];
    }
  );
}>();

const initData = reactive<{
  visible: boolean;
  mainTagList: any[];
  minorTagList: any[];
  selectMainTag: Record<string, any>;
  selectPrevMainTag: Record<string, any>;
  selectMinorTag: any[];
  showAlert: boolean;
}>({
  visible: false,
  mainTagList: [],
  minorTagList: [],
  selectMainTag: {},
  selectPrevMainTag: {},
  selectMinorTag: [],
  showAlert: true,
});

const selectMainTagId = computed(() => {
  return initData.selectMainTag?.id;
});

const selectMinorTagIdList = computed(() => {
  return initData.selectMinorTag.map((item: Record<string, any>) => {
    return item.id;
  });
});

const selectedTag = computed(() => {
  if (initData.selectMinorTag.length) {
    const lineTypeName = {
      1: "回拨",
      2: "SIP",
      3: "AXB",
      4: "固话",
      6: "虚拟A路",
    };
    return initData.selectMinorTag.reduce(
      (pre, cur) => {
        for (const key in cur.availableLineMap) {
          const lineList = cur.availableLineMap[key];
          const findIndex = pre.list.findIndex(
            (sonItem: Record<string, any>) => {
              return sonItem.key === key;
            }
          );
          if (findIndex === -1) {
            pre.list.push({
              key,
              name: lineTypeName[key],
              value: lineList,
            });
            pre.total += lineList.length;
          } else {
            pre.total -= pre.list[findIndex].value.length;
            pre.list[findIndex].value = Array.from(
              new Set([...pre.list[findIndex].value, ...lineList])
            );
            pre.total += pre.list[findIndex].value.length;
          }
        }
        return pre;
      },
      {
        list: [],
        total: 0,
      }
    );
  } else {
    return {
      list: [],
      total: 0,
    };
  }
});

// 所有的行业标签
const getMainTag = async (list: any[] = []) => {
  const { data } = await businessMainTagApi.getData();
  initData.mainTagList = businessMainTagApi.handleData(data);
  if (!list.length) {
    initData.selectMainTag = initData.mainTagList[0];
    initData.selectPrevMainTag = initData.selectMainTag;
    scrollIntoView();
    await getMinorTag();
  } else {
    const pid = list[0]?.pid;
    initData.selectMainTag = initData.mainTagList.find(
      (item) => item.id === pid
    );
    const minorTagIds = list
      .filter((item: Record<string, any>) => item.pid === pid)
      .map((item: Record<string, any>) => item.id);
    initData.selectPrevMainTag = initData.selectMainTag;
    scrollIntoView();
    await getMinorTag();
    initData.selectMinorTag = initData.minorTagList.filter((item: any) => {
      return minorTagIds.includes(item.id);
    });
  }
};

// 所有的行业次标签
const getMinorTag = async () => {
  if (!selectMainTagId.value) return;
  const { data } = await businessMinorTagApi.getData({
    parentLabelId: selectMainTagId.value,
  });
  initData.minorTagList = businessMinorTagApi.handleData(data);
};

// 点击主行业标签
function handleMainTag(item: Record<string, any>) {
  initData.selectMainTag = item;
  scrollIntoView();
  getMinorTag();
}

// 点击次行业标签
function handleMinorTag(info: Record<string, any>) {
  if (initData.selectPrevMainTag.id != initData.selectMainTag.id) {
    initData.selectMinorTag = [];
  }
  initData.selectPrevMainTag = initData.selectMainTag;
  const index = initData.selectMinorTag.findIndex(
    (item: Record<string, any>) => item.id === info.id
  );
  if (index === -1) {
    if (initData.selectMinorTag.length >= 5) {
      return ElMessage.warning("最多可选择5个标签!");
    }
    initData.selectMinorTag.push(info);
  } else {
    initData.selectMinorTag.splice(index, 1);
  }
}

function scrollIntoView() {
  if (initData.selectMainTag) {
    nextTick(() => {
      const element = document.getElementById(`${selectMainTagId.value}`);
      if (element) {
        element.scrollIntoView({ behavior: "smooth", block: "center" });
      }
    });
  }
}

function handleSubmit() {
  emits("emitFunc", {
    list: initData.selectMinorTag,
    label: initData.selectMinorTag.map((item) => item.name),
  });
  handleClose();
}

function handleClose() {
  initData.visible = false;
  initData.showAlert = true;
}

async function trigger(visible: boolean, list: Record<string, any>[]) {
  initData.visible = visible;
  await getMainTag(list);
}

defineExpose({
  trigger,
});
</script>

<style scoped lang="scss">
.business-scenarios-dialog {
  :deep(.el-dialog__body) {
    padding: 0 !important;
  }
}

.el-alert {
  padding: 10px 5px 10px 20px;
  margin-bottom: 5px;

  .alert-content {
    p {
      line-height: 22px;

      &:nth-child(2) {
        text-indent: 3em;
      }
    }
  }

  :deep(.el-alert__content .el-alert__close-btn) {
    top: 24px;
  }
}

.selected-tag {
  padding: 10px 20px;
  border-radius: 4px;
  background-color: #f5f7fa;
  color: #606266;
  font-size: 15px;
  line-height: 22px;
  margin-bottom: 15px;
  text-align: center;
}

.business-content {
  display: flex;
  height: 55vh;
  overflow: auto;
}

.group-list,
.label-list {
  height: 100%;
  overflow: auto;

  &::-webkit-scrollbar {
    width: 3px;
    height: 3px;
  }
}

.group-list {
  flex-basis: 180px;
  background-color: #f5f7fb;
  padding: 10px 10px 0 10px;

  .group-item {
    display: flex;
    position: relative;
    padding-bottom: 30px;

    &::before {
      position: absolute;
      top: 10px;
      left: 3.5px;
      content: "";
      height: 100%;
      border-left: 1px dashed #e1e4e8;
    }
    &:last-child {
      &::before {
        border-left: none;
      }
    }
    .group-name {
      display: inline-flex;
      font-size: 13px;
      line-height: 18px;
      margin-left: 15px;
      cursor: pointer;

      &::before {
        position: absolute;
        top: 0;
        left: 0;
        content: "";
        width: 8px;
        height: 8px;
        margin-top: 5px;
        border-radius: 50%;
        background-color: #9ca6b9;
        z-index: 2;
      }

      &.checked {
        color: #242934;
        font-weight: 600;

        &::before {
          background-color: #0056ff;
        }
      }
    }
  }
}

.label-list {
  flex-grow: 1;
  display: grid;
  grid-template-rows: repeat(auto-fill, 68px);
  grid-template-columns: repeat(4, 1fr);
  grid-row-gap: 20px;
  grid-column-gap: 20px;
  padding: 10px 5px 0 10px;

  .label-item {
    height: 68px;
    cursor: pointer;
    text-align: center;
    line-height: 68px;
    border-radius: 4px;
    border: 1px solid #e1e4e8;

    &.checked {
      color: #0056ff;
      border-color: #0056ff;
    }
  }
}

.table-empty {
  flex-grow: 1;
}
</style>
