import { useStore } from "vuex";
import { nextTick, computed, onMounted, onUnmounted } from "vue";
import { ElMessage } from "element-plus";
import {
  useRoute,
  useRouter,
  onBeforeRouteLeave,
  onBeforeRouteUpdate,
} from "vue-router";

// 封装model
export function useModel(
  props: Record<string, any>,
  propName: string,
  emits: any
) {
  return computed({
    get: () => {
      const type = Object.prototype.toString.call(props[propName]);
      if (type === "[object Object]") {
        // const obj = {};
        return new Proxy(props[propName], {
          set: (target, prop, value) => {
            const result = Reflect.set(target, prop, value);
            nextTick(() => {
              emits(`update:${propName}`, { ...target });
            });
            return result;
            // obj[prop] = value; // vue视图更新会在本轮事件循环结束或者下次事件循环执行。不会因为某个数据的状态改变而改变。 https://blog.csdn.net/weixin_34541572/article/details/113452939
            // emits(`update:${propName}`, {
            //   ...target,
            //   ...obj,
            //   [prop]: value,
            // });
            // return true;
          },
        });
      } else {
        return props[propName];
      }
    },
    set: (value) => {
      emits(`update:${propName}`, value);
    },
  });
}

/*
 * clipboard: Clipboard构造函数
 * copyDom: 拷贝内容的dom
 * */
export const useClipboard = (options: any) => {
  let clipboard: any = "";
  const {
    id = "",
    clipboardFail = () => {},
    clipboardSuccess = () => {},
  } = options;
  if (clipboard) {
    clipboard.destroy();
  }
  const copyDomList: any = document.getElementById(id);
  clipboard = new ClipboardJS(copyDomList);
  clipboard.on("success", function (e) {
    ElMessage.success("复制成功");
    e.clearSelection();
    clipboard = "";
    clipboardSuccess();
  });
  clipboard.on("error", function () {
    ElMessage.error("复制失败");
    clipboard = "";
    clipboardFail();
  });
  return clipboard;
};
/*
 * QRCode: QRCode构造函数
 * codeDom: 二维码内容的dom
 * */
export const useQRCode = (options) => {
  const {
    dom,
    text = "",
    width = 150,
    height = 150,
    colorDark = "#000000",
    colorLight = "#ffffff",
  } = options;
  return new QRCode(dom, {
    text: text,
    width: width,
    height: height,
    colorDark: colorDark,
    colorLight: colorLight,
    correctLevel: QRCode.CorrectLevel.H,
  });
};
/*
 * 路由相关
 * */
export const useVueRouter = () => {
  const route: Record<string, any> = useRoute();
  const router: Record<string, any> = useRouter();
  const goPage = async (params) => {
    return await router.push(params);
  };
  const backPage = (number) => {
    router.go(number);
  };
  const replacePage = (path) => {
    router.replace(path);
  };
  const addPage = (path) => {
    router.addRoute(path);
  };
  return {
    route,
    router,
    goPage,
    addPage,
    backPage,
    replacePage,
    routeQuery: computed(() => {
      return route.query ?? "";
    }),
    routeParams: computed(() => {
      return route.params ?? "";
    }),
    onMounted,
    onUnmounted,
    onBeforeRouteUpdate,
    onBeforeRouteLeave,
  };
};
/*
 * vuex 状态管理器
 * */
export const useVueStore = () => {
  const store = useStore();
  return {
    storeState: store.state,
  };
};
