import http from "@assets/http";
import { serviceName } from "@assets/common";
import { arrayHasValue } from "jz-tool-lib";



interface Params {
  companyCode: string;
  companyName: string;
  pageNo: number;
  pageSize: number;
  distributeState: string;
  numberType: string;
  parentId: string;
}

export const orderListApi = {
  getData: (params: Params) => {
    return http.get(`${serviceName.marketAdmin}/phone-num-order/orderList`, {
      params,
    });
  },
  handleData: function (data) {
    return {
      list: arrayHasValue(data.records)
        ? data.records.map((item) => {
          return {
            ...item,
          };
        })
        : [],
      total: parseInt(data.total),
    };
  },
};
export const phoneListApi = {
  getData: (params: Params) => {
    return http.get(`${serviceName.marketAdmin}/phone-num-order/phoneList`, {
      params,
    });
  },
  handleData: function (data) {
    return {
      list: arrayHasValue(data.records)
        ? data.records.map((item) => {
          if (item.city && item.city.split(",").length > 5) {
            const cityListStr = item.city.split(",").slice(0, 5).join();
            item.cityHandler = cityListStr + "...";
          }
          return {
            ...item,
          };
        })
        : [],
      total: parseInt(data.total),
    };
  },
};
export const changeApi = {
  getData: (params: Params) => {
    return http.post(
      `${serviceName.marketAdmin}/phone-num-order/changeNumber`,
      params
    );
  },
};
//绑定
export const bindApi = {
  getData: (params: Params) => {
    return http.post(
      `${serviceName.agentSystem}/phoneNumOrder/phoneBind`,
      params
    );
  },
};
//解绑
export const unbindApi = {
  getData: (params: Params) => {
    return http.post(
      `${serviceName.agentSystem}/phoneNumOrder/phoneUnbind`,
      params
    );
  },
};

// 绑定获取短信验证码
interface TestSms {
  phone: string;
  imageCode: string;
  uuid: string
}

export const sendSmsApi = {
  getData: (params: TestSms) => {
    return http.post(
      `${serviceName.baseUserRest}/userInfo/validImageCodeSendSmsCode`, params
    );
  },
};


