import http from "@assets/http";
import {
  paymentTypeMap,
  commissionTypeList,
  transactionTypeList,
  withdrawalStatusList,
} from "@assets/typeStatus";
import { handlerComboDate, serviceName } from "@assets/common";
import { arrayHasValue, arrayMatchValue } from "jz-tool-lib";
// 查询账户
export const queryAccountApi = {
  getData: () => {
    return http.get(`${serviceName.agentSystem}/assets/detail`);
  },
  handleData: (data) => {
    return {
      accountBalance: data?.accountBalance ?? 0, // 账户余额
      totalCommission: data?.totalCommission ?? 0, // 总佣金
      withdrawAble: data?.withdrawable ?? 0, // 可提现佣金
      withdrawn: data?.withdrawable ?? 0, // 已提现佣金
      frozenAmount: data?.frozenAmount ?? 0, // 冻结金额
      prepaidCall: data?.prepaidCall ?? 0, // 预付费
      qfbAmount: data?.qfbAmount ?? 0, // 企付宝账户
      totalTransactionAmount: data?.totalTransactionAmount ?? 0, // 总交易金额
    };
  },
};

// 佣金按年统计数据
export const yearCommissionApi = {
  getData: (params: { year?: string }) => {
    return http.get(
      `${serviceName.agentSystem}/commission/mouthCommissionStatisticsByYear`,
      {
        params: params,
      }
    );
  },
  handleData: (data: Array<any>) => {
    const proportionList = data.map((item) => {
      return item.amount;
    });
    const tableList = data.filter((item) => {
      item.comboDateStr = handlerComboDate(item.comboDate);
      return item.amount > 0;
    });
    return {
      proportionList,
      tableList,
    };
  },
};

// 佣金统计年度按类型分组
export const typeYearCommissionApi = {
  getData: (params: { year?: string }) => {
    return http.get(
      `${serviceName.agentSystem}/commission/mouthCommissionStatisticsGroupByParamType`,
      {
        params: params,
      }
    );
  },
  handleData: (data) => {
    return arrayHasValue(data)
      ? data.map((item) => {
          if (item.isTrue) {
            const commissionStatusInfo = arrayMatchValue(
              commissionTypeList,
              item.paramType,
              "value"
            );
            return {
              name: commissionStatusInfo?.label,
              value: item.amount,
            };
          }
        })
      : [];
  },
};

// 返佣记录
interface MouthCommission {
  comboDate: unknown;
  current: number;
  size: number;
  endTime: string;
  startTime: string;
  paramType: number | string;
}

export const mouthCommissionApi = {
  getData: (params: MouthCommission) => {
    return http.post(
      `${serviceName.agentSystem}/commission/agencyCommissionList`,
      params
    );
  },
  handleData: (data) => {
    return {
      list: arrayHasValue(data.records)
        ? data.records.map((item) => {
            const commissionInfo = arrayMatchValue(
              commissionTypeList,
              item.paramType,
              "value"
            );
            item.commissionTypeName = commissionInfo?.label;
            item.sourceTypeName = item.type === 0 ? "服务商" : "客户";
            return item;
          })
        : [],
      total: parseInt(data.total),
    };
  },
};

