// 查询产品返佣、代理商同级返佣详情、查询话费返佣详情
interface AppCommissionDetail {
  commissionId: string;
}

export const appCommissionDetailApi = {
  getData: (params: AppCommissionDetail) => {
    return http.get(
      `${serviceName.agentSystem}/commission/getAppCommissionDetail/${params.commissionId}`
    );
  },
  handleData: (data) => {
    const info = {
      orderNo: data?.orderNo,
      appName: data?.appName,
      salePrice: data?.salePrice,
      realPrice: data?.realPrice,
      amount: data?.amount,
      thawAmount: data?.thawAmount,
      buyTime: data?.buyTime,
      percent: data?.percent,
      seat: data?.seat,
      proxyPrice: data?.proxyPrice,
      payableCharge: data?.payableCharge,
      realConsumption: data?.realConsumption,
      lowestConsumption: data?.lowestConsumption,
      ownCallPrice: data?.ownPrice,
      belowCallPrice: data?.belowPrice,
      percentStr: data?.percent ? `${data?.percent}%` : "",
    };
    const amountStatusMap = new Map([
      [0, { label: "冻结", styleType: "danger" }],
      [1, { label: "解冻", styleType: "success" }],
      [2, { label: "已取消", styleType: "info" }],
    ]);
    const recodeList = arrayHasValue(data?.recodeList)
      ? data.recodeList.map((item, index) => {
          const amountStatusInfo = amountStatusMap.get(item.amountStatus);
          return {
            stage: `第${index + 1}期`,
            endDate: item.endDate,
            realityEndDate: item.amountStatus === 1 ? item.endDate : "-",
            amount: item.amount,
            amountStatus: item.amountStatus,
            amountStatusName: amountStatusInfo?.label,
            amountStatusStyle: amountStatusInfo?.styleType,
          };
        })
      : [];
    return {
      info,
      list: [info],
      recodeList,
    };
  },
};

// 代理商订单详情
export const agentOrderDetailApi = {
  getData: (params: { orderNo: number }) => {
    return http.get(
      `${serviceName.agentSystem}/order/detail/${params.orderNo}`
    );
  },
  handleData: (data) => {
    const info = {
      price: data.price,
      orderNo: data.orderNo,
      createdTime: data.payTime,
      paymentType: data.paymentType,
      paymentTypeName: paymentTypeMap.get(data.paymentType),
    };
    return data ? [info] : [];
  },
};

// 获取提现记录
interface WithdrawalRecord {
  checkStatus: number;
  endTime: string;
  startTime: string;
}

export const withdrawalRecordApi = {
  getData: (params: WithdrawalRecord) => {
    return http.post(`${serviceName.agentSystem}/cashOut/list`, params);
  },
  handleData: (data) => {
    return {
      list: data.records
        ? data.records.map((item) => {
            const withdrawalStatusInfo = arrayMatchValue(
              withdrawalStatusList,
              item.checkStatus,
              "value"
            );
            return {
              id: item.id,
              cashOutNo: item.cashOutNo,
              createdTime: item.createdTime,
              amount: item.amount,
              type: item.type,
              withdrawTypeName: paymentTypeMap.get(item.type),
              checkStatus: item.checkStatus,
              checkStatusName: withdrawalStatusInfo?.label,
              checkStatusStyleType: withdrawalStatusInfo?.styleType,
            };
          })
        : [],
      total: parseInt(data.total),
    };
  },
};

// 提交提现申请
interface SubmitWithdrawal {
  accountBalance: number;
  account: string;
  amount: string;
  cashOutNo: string;
  type: number;
}

export const submitWithdrawalApi = {
  getData: (params: SubmitWithdrawal) => {
    return http.post(`${serviceName.agentSystem}/cashOut/add`, params);
  },
};
// 银行账号列表查询
export const bankAccountApi = {
  getData: () => {
    return http.post(`${serviceName.agentSystem}/bankAccount/list`);
  },
  handleData: (data: Array<any>) => {
    return data
      ? data.map((item) => {
          item.nameAccount = `${item.bankName}(${item.account.slice(-4)})`;
          return item;
        })
      : [];
  },
};

// 生成交易单号
export const generatorNumberApi = {
  getData: () => {
    return http.post(`${serviceName.agentSystem}/cashOut/getGeneratorNumber`);
  },
};

// 交易记录
interface TransactionRecord {
  size: number;
  current: number;
  tradeType: number | string;
  endTime: string;
  startTime: string;
}

export const transactionRecordApi = {
  getData: (params: TransactionRecord) => {
    return http.post(`${serviceName.agentSystem}/cashOut/tradeList`, params);
  },
  handleData: (data) => {
    return {
      list: arrayHasValue(data.records)
        ? data.records.map((item) => {
            const transactionInfo = arrayMatchValue(
              transactionTypeList,
              item.tradeType,
              "value"
            );
            return {
              id: item.id,
              amount: item.amount,
              createdTime: item.createdTime,
              commissionId: item.commissionId,
              cashOutNo: item.cashOutNo,
              tradeType: item.tradeType,
              tradeTypeName: transactionInfo?.label,
              paymentType: item.type,
              paymentTypeName: paymentTypeMap.get(item.type),
            };
          })
        : [],
      total: parseInt(data.total),
    };
  },
};

