import http from "@assets/http";
import { serviceName } from "@assets/common";
import { arrayHasValue } from "jz-tool-lib";

// 获得当前登录代理商的资格认证状态
export const certificationStatusApi = {
  getData: () => {
    return http.post(`${serviceName.agentSystem}/user/getCertificationStatus`);
  },
};
// 修改信息为已读状态
export const updateMsgReadStateApi = {
  getData: (params: { state: number }) => {
    return http.put(
      `${serviceName.agentSystem}/user/updateMsgReadState/${params.state}`
    );
  },
};
// 查询代理产品代理权过期信息
export const expireInformationApi = {
  getData: () => {
    return http.post(`${serviceName.agentSystem}/order/expireInformation`);
  },
};
// 查询当前代理商的级别(当前登录用户)
export const agencyLevelApi = {
  getData: () => {
    return http.get(`${serviceName.agentSystem}/user/getHighestAgencyLevel`);
  },
};
// 用户购买协议
export const agreementApi = {
  getData: (params: { type: number }) => {
    return http.get(
      `${serviceName.agentSystem}/agreement/detail?type=${params.type}`
    );
  },
  handleData: (data) => {
    return data.content ? data.content.replace(/\n/g, "<br/>") : "";
  },
};

// 根据公司名称模糊查询（在数企注册）
interface SearchCompany {
  companyName: string;
  pageNo: number;
  pageSize: number;
}

export const searchCompanyApi = {
  getData: (params: SearchCompany) => {
    return http.get(
      `${serviceName.marketAdmin}/company-info/getCompanyInfoByCompanyName`,
      {
        params: params,
      }
    );
  },
  handleData: (data) => {
    return {
      list: arrayHasValue(data.records) ? data.records : [],
      total: data.total,
    };
  },
};

// 根据公司名称模糊查询（在工商局注册）
export const searchBizCompanyApi = {
  getData: (companyName: string) => {
    return http.get(
      `${serviceName.baseBizRest}/baseBiz/companyBaseInfo/queryCompanyListByLikeName?name=${companyName}`
    );
  },
  handleData: (data) => {
    return {
      list: arrayHasValue(data)
        ? data.map((item) => {
            item.companyName = item.companyName.replace(
              /<strong>|<\/strong>/g,
              ""
            );
            return item;
          })
        : [],
    };
  },
};
// 根据公司id获取公司基础信息（在工商局注册）
export const searchBizCompanyInfoApi = {
  getData: (companyId: string) => {
    return http.get(
      `${serviceName.baseBizRest}/baseBiz/companyBaseInfo/details?id=${companyId}`
    );
  },
  handleData: (data) => {
    return data ? JSON.parse(data) : "";
  },
};

/*
 * 图片上传
 * */
export const ossVipUploadFileApi = {
  getData: (params: Record<string, any>) => {
    return http.post(`${serviceName.baseOssRest}/oss/uploadFile`, params, {
      headers: {
        "content-type": "application/x-www-form-urlencoded;charset=UTF-8",
      },
    });
  },
  handleData: (data: string[]) => {
    return data ? data[0] : "";
  },
};
/*
 * 图片完整路径
 * */
export const FileUrlApi = {
  getData: (fileId: string) => {
    return http.get(
      `${serviceName.baseOssRest}/oss/getUrlByFileId?fileId=${fileId}`
    );
  },
};

/*
 * 获取图片验证码
 * */
export function codeImageApi() {
  return (
    window.origin +
    `${serviceName.bdsaas}/ajax/main/getcodeimage.do?t=${new Date().getTime()}`
  );
}
/*
 * 获取图片验证码2
 * */
export function codeImageApi2() {
  return http.get(`${serviceName.baseUserRest}/userInfo/getImage`);
}

/**
 * 获取公司发票抬头列表
 */
export const queryInvoiceHeaderListApi = {
  getData(params) {
    return http.get(
      `${serviceName.baseBizRest}/baseBiz/companyBaseInfo/queryCompanyInfoByName`,
      { params }
    );
  },
  handleData(data) {
    return data.map((item) => {
      return {
        id: item.id,
        label: item.companyName.replace(/<strong>|<\/strong>/g, ""),
      };
    });
  },
};

