/**
 * 获取开票信息列表
 */
export const queryInvoiceListApi = {
  typeMap: {
    1: "普通发票",
    3: "增值税发票",
  },
  getData(params) {
    return http.get(`${serviceName.baseUpmsRest}/company-invoice-info/list`, {
      params,
    });
  },
  handleData(data) {
    return data.map((item) => {
      return {
        ...item,
        typeLabel: this.typeMap[item.type],
      };
    });
  },
};

