import http from "@assets/http";
import { serviceName } from "@assets/common";
import { arrayHasValue } from "jz-tool-lib";

//统计公司的总分钟数
type BaseMinutes = {
  startTime?: string;
  endTime?: string;
};
export const getDirectMinutesApi = {
  getData: (params: BaseMinutes & { interfaceData: string }) => {
    return http.post(
      `${serviceName.agentSystem}/analysis/callRecord/getTotalMinutes`,
      params
    );
  },
  handleData: function (data) {
    return {
      customer: data.customer || "0",
      interface: data.interface || "0",
    };
  },
};

export const getMinutesApi = {
  getData: (
    params: BaseMinutes & { businessType: string; reportType: string }
  ) => {
    return http.post(
      `${serviceName.agentSystem}/agencyAnalysisCallDay/findAgencyAllMinute`,
      params
    );
  },
  handleData: function (data) {
    return data ?? "0";
  },
};

// 直客
type BaseSummary = {
  pageNo: number;
  pageSize: number;
  startTime?: string;
  endTime?: string;
};
export const getDirectSummaryApi = {
  getData: (params: BaseSummary & { interfaceType: string }) => {
    return http.post(
      `${serviceName.agentSystem}/analysis/callRecord/getSummaryMessage`,
      params
    );
  },
  handleData: function (data) {
    return {
      list: arrayHasValue(data.records)
        ? data.records.map((item) => {
            return {
              ...item,
              seatSize: item.seatAll,
              date: item.startTime + " 至 " + item.endTime,
            };
          })
        : [],
      total: parseInt(data.total),
    };
  },
};

// 接口
export const getSummaryApi = {
  getData: (
    params: BaseSummary & { businessType: string; reportType: string }
  ) => {
    return http.post(
      `${serviceName.agentSystem}/agencyAnalysisCallDay/findAgencyReport`,
      params
    );
  },
  handleData: function (data) {
    return {
      list: arrayHasValue(data.records)
        ? data.records.map((item) => {
            return {
              ...item,
              date: item.startTime + " 至 " + item.endTime,
            };
          })
        : [],
      total: parseInt(data.total),
    };
  },
};
