<template>
  <!--  支付二维码  -->
  <el-dialog
    class="custom-dialog payment-dialog"
    width="460px"
    lock-scroll
    append-to-body
    :close-on-click-modal="false"
    :title="`${paymentTypeName}付款`"
    :model-value="visible"
    @open="handleOpen"
    @close="handleClose"
  >
    <div class="body-container" v-show="paymentInfo.orderState === 0">
      <div class="QR-code" id="qrcode"></div>
      <p class="word">付款金额</p>
      <p class="amount">¥ {{ amount }}</p>
      <p class="prompt">
        请使用
        {{ paymentTypeName }} 扫一扫二维码完成支付
      </p>
      <el-button type="primary" color="#0056ff" @click="confirmPayment"
        >完成支付
      </el-button>
    </div>
    <div class="body-container" v-show="paymentInfo.orderState === 1">
      <p class="notice">
        您已有该商品订单，无法再次下单，请前往订单页完成支付。
      </p>
      <el-button
        type="primary"
        @click="goPage('/marketService/personalOrder/list')"
        >前往订单
      </el-button>
    </div>
  </el-dialog>
</template>
<script lang="ts" setup>
import { ElMessage } from "element-plus";
import { computed, shallowReactive } from "vue";

import { paymentTypeMap } from "@assets/typeStatus";
import { useQRCode, useVueRouter } from "@assets/useHook";

import { confirmPaymentApi } from "@api/payment-api";

const props = defineProps<{
  visible: boolean;
  amount?: string | number;
  paymentInfo: Record<string, any>;
}>();
const emits = defineEmits<{
  (e: "update:visible", value: boolean);
}>();

const { goPage } = useVueRouter();

const paramsData = shallowReactive({
  paymentVisible: false,
  qrcode: "",
});

const paymentTypeName = computed(() => {
  return paymentTypeMap.get(props.paymentInfo?.paymentType);
});

const handleOpen = () => {
  (document as any).getElementById("qrcode").innerHTML = "";
  if (props.paymentInfo.orderState === 0) {
    getQRCode(props.paymentInfo.payUrl);
  }
};
const handleClose = () => {
  emits("update:visible", false);
};
const confirmPayment = () => {
  const params = {
    orderNo: props.paymentInfo.orderNo,
  };
  confirmPaymentApi.getData(params).then((res) => {
    if (res.data === "success") {
      emits("update:visible", false);
      goPage("/personalCenter");
    } else {
      ElMessage.error("请尽快完成支付");
    }
  });
};
const getQRCode = (paymentUrl) => {
  paramsData.qrcode = useQRCode({
    dom: "qrcode",
    text: paymentUrl,
  });
};
</script>

<style lang="scss" scoped>
.body-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 115px;

  .QR-code {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 168px;
    height: 168px;
    margin-bottom: 40px;
    border: 1px solid #e1e4e8;
  }

  .word {
    font-size: 14px;
    color: #242934;
    line-height: 1;
  }

  .amount {
    font-size: 26px;
    font-weight: 600;
    color: #f12525;
    line-height: 1;
    margin: 12px 0 28px;
  }

  .prompt {
    font-size: 14px;
    color: #9ca6b9;
    line-height: 1;
    margin-bottom: 40px;
  }
  .notice {
    width: 300px;
    text-align: center;
    font-size: 15px;
    color: #f12525;
    line-height: 20px;
    margin-bottom: 42px;
  }
  .no-finish {
    color: #f12525;
    font-size: 14px;
    margin-top: 5px;
  }
}
</style>
<style lang="scss">
.payment-dialog.custom-dialog.el-dialog {
  .el-dialog__body {
    padding: 40px 0 50px;
  }
}
</style>
