<template>
  <div class="upload-file">
    <el-upload
      class="upload"
      name="files"
      :limit="limit"
      :accept="accept"
      :multiple="multiple"
      :file-list="defaultFileList"
      :list-type="listType"
      :data="params.uploadData"
      :action="params.uploadUrl"
      :headers="params.uploadHeader"
      :show-file-list="showFileList"
      :before-upload="beforeUpload"
      :on-success="handleSuccess"
      :on-remove="handleRemove"
      :on-exceed="handleExceed"
      :on-preview="handlePreview"
    >
      <template v-if="listType !== 'picture-card'">
        <el-button type="primary">{{ buttonWord }}</el-button>
      </template>
      <template v-else>
        <el-icon>
          <Plus />
        </el-icon>
      </template>
      <template #tip>
        <div v-if="tip" class="tip">{{ tip }}</div>
      </template>
    </el-upload>
    <slot name="example"></slot>
    <el-dialog
      width="700px"
      class="custom-dialog"
      title="预览"
      @closed="closePreview"
      v-model="initData.previewVisible"
    >
      <div class="img-box">
        <img :src="initData.previewUrl" alt="" />
      </div>
    </el-dialog>
  </div>
</template>

<script setup lang="ts">
import { shallowReactive, shallowReadonly } from "vue";
import { ElMessage } from "element-plus";
import { Plus } from "@element-plus/icons-vue";

import { serviceName, burglarproof, regExpExtensionName } from "@assets/common";
import { LocalStorage, SessionStorage } from "jz-tool-lib";
import { useVueRouter } from "@assets/useHook";

const props = withDefaults(
  defineProps<{
    buttonWord?: string; // 按钮文字
    tip?: string; // 提示文案
    limit?: number; // 上传图片数量
    maxVolume?: number; // 图片最大Mb
    accept?: string; // 图片接受类型
    multiple?: boolean; // 是否支持多选
    showFileList?: boolean; // 是否显示已上传文件列表
    defaultFileList?: any[]; // 默认上传文件
    listType?: "text" | "picture" | "picture-card"; // 文件列表的类型
    editData?: any; // 动态表单需要
  }>(),
  {
    buttonWord: "上传",
    tip: "",
    limit: 1,
    maxVolume: 1,
    multiple: false,
    listType: "text",
    accept: "image/*",
    showFileList: true,
    defaultFileList: () => [],
    editData: () => {},
  }
);
const emits = defineEmits<{
  (e: "emitUpload", value: any): void;
  (e: "update:defaultFileList", fileList: any): void;
}>();
const { bd_userInfo: userInfo, bd_authorization: authorization } =
  LocalStorage.batchGetItem(["bd_userInfo", "bd_authorization"]);
const { goPage } = useVueRouter();
const initData = shallowReactive({
  previewUrl: "",
  previewVisible: false,
});
let burglarproofInfo = burglarproof();
const params = shallowReadonly({
  uploadHeader: {
    Authorization: "Bearer " + authorization,
    xwsse: burglarproofInfo.XWsse,
    timestamp: burglarproofInfo.timestamp,
  },
  uploadData: {
    createUser: userInfo?.profileId,
  },
  uploadUrl: `${serviceName.baseOssRest}/oss/uploadFile`,
});
// 上传前
const beforeUpload = (file: Record<string, any>) => {
  burglarproofInfo = burglarproof();
  params.uploadHeader.xwsse = burglarproofInfo.XWsse;
  params.uploadHeader.timestamp = burglarproofInfo.timestamp;
  const isLtMaxVolume = file.size / 1024 / 1024 < props.maxVolume;
  if (!isLtMaxVolume) {
    ElMessage.warning(`上传文件大小不能超过 ${props.maxVolume}MB!`);
  }
  return isLtMaxVolume;
};
// 上传成功
const handleSuccess = (
  response: Record<string, any>,
  file: Record<string, unknown>,
  fileList: any[]
) => {
  if (response.statusCode === 0) {
    ElMessage.success("上传成功");
    updateImage(fileList);
  } else {
    const { msg, statusCode } = response;
    ElMessage.warning(msg ?? "");
    if (statusCode === 80001) {
      const loginForm = LocalStorage.getItem("bd_loginForm");
      LocalStorage.clear();
      SessionStorage.clear();
      loginForm && LocalStorage.setItem("bd_loginForm", loginForm);
      goPage("/");
    }
  }
};
// 移除图片
const handleRemove = (file: Record<string, any>, fileList: any[]) => {
  updateImage(fileList);
};
// 打开图片预览
const handlePreview = (uploadFile: any) => {
  const fileName: string = uploadFile.raw.name ?? "";
  if (fileName.endsWith(regExpExtensionName(fileName))) {
    const blob = new Blob([uploadFile.raw]);
    initData.previewUrl = window.URL.createObjectURL(blob);
    initData.previewVisible = true;
  }
};
// 关闭图片预览
const closePreview = () => {
  window.URL.revokeObjectURL(initData.previewUrl);
};
// 超出图片数量
const handleExceed = () => {
  ElMessage.warning(`文件最多上传数量为【${props.limit}】`);
};
// 更新图片
const updateImage = (fileList) => {
  emits("update:defaultFileList", fileList);
  const filedList = fileList.map((item) => {
    return item.response?.data ?? item.fileId;
  });
  if (fileList.length) {
    const ids = filedList.flat(1);
    // 动态表单需要
    if (props.editData?.propertyKey) {
      const params = {
        key: props.editData.propertyKey,
        ids: ids.join(","),
      };
      emits("emitUpload", params);
    } else {
      emits("emitUpload", ids);
    }
  } else {
    if (props.editData?.propertyKey) {
      const params = {
        key: props.editData.propertyKey,
        ids: "",
      };
      emits("emitUpload", params);
    } else {
      emits("emitUpload", []);
    }
  }
};
</script>
<style lang="scss" scoped>
.tip {
  font-size: 13px;
  color: #f12525;
  margin-top: 10px;
  line-height: 20px;
}

:deep(.el-upload-list--picture-card) {
  .el-upload-list__item,
  .el-upload--picture-card {
    width: 120px;
    height: 120px;
  }

  .el-upload--picture-card .el-icon {
    font-size: 18px;
  }
}

:deep(.el-upload-list.el-upload-list--text) {
  width: 400px;

  .el-upload-list__item .el-progress {
    top: 30px;
  }
}

.img-box {
  width: 650px;

  img {
    width: 100%;
  }
}
</style>
