import http from "@assets/http";
import store from "@/store";
import { ElMessage } from "element-plus";
import { serviceName } from "@assets/common";
import { LocalStorage } from "jz-tool-lib";
// 导出表格封装
function downloadExcel(url: string, fileName: string) {
  http
    .get(url, {
      headers: {
        Authorization: `Bearer ${LocalStorage.getItem("bd_authorization")}`,
        ResponseFormat: "arraybuffer",
      },
      responseType: "arraybuffer",
    })
    .then((res) => {
      const blob = new Blob([res.data], {
        type: " application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
      });
      const fileUrl = window.URL.createObjectURL(blob);
      const a = document.createElement("a");
      a.href = fileUrl;
      a.download = fileName;
      a.click();
      window.URL.revokeObjectURL(url);
    });
}
// 返佣统计导出
export const exportMouthStatisticsExcelApi = (params, name: string) => {
  return downloadExcel(
    `${serviceName.agentSystem}/export/mouthStatisticsExcel?year=${params}`,
    name
  );
};
