//下载文件
export function ossDownLoad(params) {
  const url = `${window.origin}${serviceName.baseOssRest}/ossvip/download?fileId=${params.fileId}&token=${store.getters.token}&COMPANYID=${params.companyId}`;
  window.open(url, "_blank");
}
/**
 * 下载附件
 * {api} 接口
 * {method} 请求方式
 * {filename} 下载文件名称
 * {formData} 请求参数
 * {headers} 请求头参数
 */
export async function downloadFile({
  api,
  method = "get",
  filename = "",
  formData = {},
  headers = {},
}) {
  let result: any;
  if (method === "get") {
    result = await http.get(api, {
      responseType: "blob",
      headers: {
        ResponseFormat: "blob",
      },
    });
  } else {
    result = await http.post(api, formData, {
      responseType: "blob",
      headers: {
        "content-type": "application/x-www-form-urlencoded;charset=UTF-8",
        ResponseFormat: "blob",
        ...headers,
      },
    });
  }
  const responseText = await result.data.text();
  try {
    const responseData = JSON.parse(responseText);
    if (!responseData || (responseData && responseData.statusCode !== 0)) {
      ElMessage.error(responseData.msg);
    }
  } catch (e) {
    let defaultName = "";
    if (Reflect.has(result.headers, "content-disposition")) {
      defaultName =
        result.headers["content-disposition"].split("filename=")[1] ?? "";
    }
    const a = document.createElement("a");
    const url = window.URL.createObjectURL(result.data);
    a.href = url;
    if (filename) {
      a.download = filename;
    } else {
      a.download = decodeURI(defaultName);
    }
    a.click();
    window.URL.revokeObjectURL(url);
  }
}
