import http from "@assets/http";
import { agencyLevelList } from "@assets/typeStatus";
import { serviceName } from "@assets/common";
import { arrayHasValue, arrayMatchValue } from "jz-tool-lib";
// 产品列表
interface ProductList {
  current?: number;
  size?: number;
  mode?: string;
  productId?: number;
  productName?: string;
  timeLength?: string;
}

export const productListApi = {
  getData: (params?: ProductList) => {
    return http.get(`${serviceName.agentSystem}/product/listWithApp`, {
      params: params,
    });
  },
  handleData: (data) => {
    return {
      list: arrayHasValue(data.records) ? data.records : [],
      total: parseInt(data.total),
    };
  },
};
// 查询产品详情
export const productDetailApi = {
  getData: (params: { productId: string }) => {
    return http.get(
      `${serviceName.agentSystem}/product/newDetail/${params.productId}`
    );
  },
  handleData: (data) => {
    data.timeList = [
      {
        label: data.timeLength + "年",
        value: data.timeLength,
      },
    ];
    if (arrayHasValue(data.agencyProductAttaches)) {
      data.agencyProductAttaches.forEach((item) => {
        if (item.type === 1) {
          data.procudtPrimaryImg = data.agencyProductAttaches[0]?.attachUrl;
        } else if (item.type === 2) {
          data.procudtSecondaryImg = data.agencyProductAttaches[1]?.attachUrl;
        } else if (item.type === 3) {
          data.procudtDetailImg = data.agencyProductAttaches[2]?.attachUrl;
        }
      });
    }
    return data;
  },
};
// 系列订单是否订购(当前登录用户)
export const existBuyApi = {
  getData: (params: { productId: string }) => {
    return http.get(
      `${serviceName.agentSystem}/order/existByAgencyIdAndSeriesId/${params.productId}`
    );
  },
  handleData: (data) => {
    return data;
  },
};
// 查询父级代理商的级别(当前登录用户)
export const parentAgencyLevelApi = {
  getData: () => {
    return http.get(`${serviceName.agentSystem}/user/getParentAgencyLevel`);
  },
  handleData: (data) => {
    return {
      level: data,
      levelList: data === 0 ? agencyLevelList : agencyLevelList.slice(data - 1),
    };
  },
};

//我的代理产品
export const agentProductApi = {
  getData: (params) => {
    return http.post(
      `${serviceName.agentSystem}/order/userProductList`,
      params,
      {
        headers: {
          "content-type": "application/x-www-form-urlencoded;charset=UTF-8",
        },
      }
    );
  },
  handleData: (data) => {
    return {
      list: arrayHasValue(data.records)
        ? data.records.map((item) => {
            const agencyLevelInfo = arrayMatchValue(
              agencyLevelList,
              item.agencyLevel,
              "value"
            );
            item.agencyLevelName = agencyLevelInfo?.label;
            item.totalSetsStr = item.mode === 3 ? item.totalSets : "无赠送套数";
            return item;
          })
        : [],
      total: parseInt(data.total),
    };
  },
};
// 代理产品详情
export const agentProductDetailApi = {
  getData: (orderId: string) => {
    return http.get(
      `${serviceName.agentSystem}/order/proxyProductDetail/${orderId}`
    );
  },
  handleData: (data) => {
    const { ecSwitch, orderAppList, companyKeySecret } = data;
    const productList = orderAppList
      ? orderAppList.map((item) => {
          const agencyLevelInfo = arrayMatchValue(
            agencyLevelList,
            item.agencyLevel,
            "value"
          );
          item.agencyLevelName = agencyLevelInfo?.label;
          return item;
        })
      : [];
    return {
      ecSwitch,
      productList,
      companyKeySecret: companyKeySecret ?? "",
    };
  },
};

//我的订单
export const agentOrderApi = {
  getData: (params) => {
    return http.post(
      `${serviceName.agentSystem}/order/pageMyAgencyOrder`,
      params
    );
  },
  handleData: (data) => {
    return {
      list: arrayHasValue(data.records)
        ? data.records.map((item) => {
            const agencyLevelInfo = arrayMatchValue(
              agencyLevelList,
              item.agencyLevel,
              "value"
            );
            item.agencyLevelName = agencyLevelInfo?.label;
            return item;
          })
        : [],
      total: parseInt(data.total),
    };
  },
};
// 取消订单
export const cancelOrderApi = {
  getData: (orderNo: string) => {
    return http.post(`${serviceName.agentSystem}/order/closedOrder/${orderNo}`);
  },
};
