<template>
  <header class="login-register-header">
    <div
      class="logo"
      :style="{
        visibility: ['ec-line'].includes(systemName) ? 'hidden' : 'visible',
      }"
    ></div>
    <div class="info">
      <span class="words">{{ info.words }}</span>
      <span class="button" @click="handleJumpPage">
        <span>{{ info.button }}</span>
        <el-icon><arrow-right /></el-icon>
      </span>
    </div>
  </header>
</template>

<script lang="ts" setup>
import { computed } from "vue";
import { systemName } from "@assets/common";
import { useVueRouter } from "@/assets/useHook";
import { ArrowRight } from "@element-plus/icons-vue";
const props = defineProps<{
  pageName: string;
}>();
const { goPage } = useVueRouter();
const info = computed(() => {
  if (props.pageName === "login") {
    return {
      words: "还没账号？",
      button: "去注册",
    };
  } else {
    return {
      words: "已有账号，",
      button: "立即登录",
    };
  }
});
// 跳转页面
const handleJumpPage = () => {
  let path: string;
  if (props.pageName === "login") {
    path = "/register";
  } else {
    path = "/login";
  }
  goPage(path);
};
</script>

<style lang="scss" scoped>
.login-register-header {
  position: absolute;
  top: 0;
  z-index: 2;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  padding: 0 280px;
  background: #ffffff;

  .logo {
    width: 154px;
    height: 23px;
    background: url("../../assets/images/logo-header.png") no-repeat center;
    background-size: cover;
  }

  .info {
    display: flex;
    align-items: center;
    font-size: 14px;
    color: #9ca6b9;
    .button {
      display: flex;
      align-items: center;
      color: #0056ff;
      cursor: pointer;
      .el-icon {
        font-size: 15px;
      }
    }
  }
}
</style>
