import http from "@assets/http";
import { serviceName } from "@assets/common";
import { arrayHasValue } from "jz-tool-lib";
// 查询当前登录代理商详情
export const agentInfoApi = {
  getData: () => {
    return http.get(`${serviceName.agentSystem}/user/currentUserDetail`);
  },
  handleData: (data) => {
    return data;
    // return {
    //   companyName: data.companyName ? data.companyName : "--",
    //   userName: data.userName,
    //   mobilePhone: data.mobilePhone,
    //   addressAll: data.addressAll ? data.addressAll : "--",
    // };
  },
};
// 获取绑定银行账户列表
export const bankAccountListApi = {
  getData: () => {
    return http.post(`${serviceName.agentSystem}/bankAccount/list`);
  },
  handleData: (data) => {
    return arrayHasValue(data) ? data : [];
  },
};
// 绑定银行卡
interface addAccountInterface {
  account: string;
  bankName: string;
  ownerName: string;
  id?: string;
}
export const addAccountApi = {
  getData: (params: addAccountInterface) => {
    return http.post(`${serviceName.agentSystem}/bankAccount/add`, params);
  },
};
// 修改银行卡
export const modifyAccountApi = {
  getData: (params: addAccountInterface) => {
    return http.post(`${serviceName.agentSystem}/bankAccount/update`, params);
  },
};
// 删除银行卡
export const deleteAccountApi = {
  getData: (params: { id: string }) => {
    return http.delete(
      `${serviceName.agentSystem}/bankAccount/delete/${params.id}`
    );
  },
};

// 产品返佣体系
export const userProxyProductApi = {
  getData: () => {
    return http.get(
      `${serviceName.agentSystem}/product/userProxyProductInfoList`
    );
  },
  handleData: (data) => {
    const allList: typeof data = [];
    data.map((item) => {
      item.appList.map((sonItem, sonIndex) => {
        sonItem.productName = item.productName;
        if (sonIndex === 0) {
          sonItem.appListLength = item.appList.length;
        }
        allList.push(sonItem);
      });
    });
    return allList;
  },
};
