<template>
  <div class="sum-price">
    <div class="price-content" v-if="props.canBuy">
      <span class="buy-price">&yen;{{ props.buyPrice }}</span>
    </div>
    <template
      v-if="
        ['CLOUD_CALL_PACKAGE', 'CLOUD_CALL_PACKAGE_BIG'].includes(
          routeQuery.serviceCode
        )
      "
    >
      <el-button
        type="primary"
        @click="handleBuy"
        :disabled="!props.canBuy || !props.hasAgentPrice || isOvertake"
        >立即购买</el-button
      >
    </template>
    <template v-else>
      <el-button
        type="primary"
        @click="handleBuy"
        :disabled="!props.canBuy || !props.hasAgentPrice"
        >立即购买</el-button
      >
    </template>
    <p class="tip" v-if="!props.canBuy">{{ props.tip }}</p>
  </div>
</template>

<script setup lang="ts">
import { useVueRouter } from "@assets/useHook";
const { routeQuery } = useVueRouter();
const props = withDefaults(
  defineProps<{
    tip?: string;
    canBuy: boolean;
    buyPrice: string;
    isOvertake?: boolean;
    hasAgentPrice: boolean;
  }>(),
  {
    tip: "你已购买同类型产品，请勿重复购买",
    canBuy: false,
    buyPrice: "0.00",
    isOvertake: false, // 大客户套餐、呼叫套餐购买金额不能小于最低消费金额
    hasAgentPrice: false, // 是否配置代理价
  }
);
const emits = defineEmits<{
  (e: "emitFunc", value?: Record<string, any>);
}>();
function handleBuy() {
  emits("emitFunc");
}
</script>

<style scoped lang="scss">
.sum-price {
  padding: 30px 190px 0;
  box-shadow: 0 -10px 20px 0px #646b7a14;
  .price-content {
    @include flex(initial, center, column);
    margin-bottom: 30px;
  }
  .buy-price {
    font-weight: 600;
    font-size: 24px;
    line-height: 38px;
    color: #f12525;
  }
  .el-button {
    width: 120px;
    &.is-disabled {
      opacity: 0.3;
    }
  }
  .tip {
    color: #9ca6b9;
    font-size: 14px;
    line-height: 24px;
    margin-top: 20px;
  }
}
</style>
