class Echarts {
  private myEcharts;
  protected options: Record<string, unknown> = {};

  constructor(chartDom, options) {
    this.options = options;
    this.init(chartDom);
  }

  init(chartDom) {
    this.myEcharts = echarts.getInstanceByDom(chartDom);
    if (!this.myEcharts) {
      this.myEcharts = echarts.init(chartDom);
    }
    this.handleOption();
  }

  handleOption() {
    this.myEcharts.setOption(this.options);
  }

  dispose() {
    echarts.dispose(this.myEcharts);
  }
}

// 获取Echarts实例
export const initEcharts = function (chartDom, options) {
  return new Echarts(chartDom, options);
};

export class Updater {
  timer;
  prevData; // 上一次
  nextData; // 下一次
  option: Record<string, any>;
  dispatch: Record<string, any[]>;

  constructor(option) {
    this.dispatch = {};
    this.option = option;
    this.init();
    this.interval(option?.time);
  }

  init() {
    this.prevData = this.getData();
  }

  // 获取数据
  async getData(api?: any) {
    const data = await api.getData();
    return api.handleData(data);
  }

  on(key: "no-update" | "update", fn: any) {
    if (!this.dispatch[key]) {
      this.dispatch[key] = [];
    }
    this.dispatch[key].push(fn);
    return this;
  }
  off() {
    clearInterval(this.timer);
  }
  // 轮询
  interval(time = 1000) {
    this.timer = setInterval(() => {
      this.nextData = this.getData(this.option.api);
      this.comparison(this.prevData, this.nextData);
    }, time);
  }

  comparison(prevData, nextData) {
    if (prevData === nextData) {
      this.dispatch["update"].forEach((fn) => {
        fn();
      });
    } else {
      this.dispatch["no-update"].forEach((fn) => {
        fn();
      });
    }
  }
}
// 日期禁止选择范围
export class DisabledDate {
  firstDateTime;
  cycleTimes;
  constructor(cycle = 30) {
    this.cycleTimes = cycle * 1000 * 60 * 60 * 24;
  }
  handleCalendar(dateList) {
    if (dateList[0] && !dateList[1]) {
      this.firstDateTime = dateList[0]; //记录选中的首个日期
    } else {
      this.firstDateTime = "";
    }
  }
  handleDisabled(time) {
    if (this.firstDateTime) {
      return (
        time.getTime() < this.firstDateTime.getTime() - this.cycleTimes ||
        time.getTime() > this.firstDateTime.getTime() + this.cycleTimes ||
        time.getTime() > new Date().getTime()
      );
    } else {
      return time.getTime() > new Date().getTime();
    }
  }
}
