<template>
  <el-menu
    router
    :default-active="initData.defaultMenu"
    class="custom-sub-menu"
  >
    <template v-if="isGroup">
      <el-menu-item-group
        v-for="(value, index) in list"
        :key="index"
        :title="value.moduleTitle"
      >
        <template
          v-for="sonItem in value[value.moduleTitle]"
          :key="sonItem.path"
        >
          <el-menu-item v-if="!sonItem.meta?.hideMenu" :index="sonItem.path"
            >{{ sonItem.meta?.pageName }}
          </el-menu-item>
        </template>
      </el-menu-item-group>
    </template>
    <template v-else>
      <template v-for="value in list" :key="value.path">
        <el-menu-item v-if="!value.meta?.hideMenu" :index="value.path"
          >{{ value.meta?.pageName }}
        </el-menu-item>
      </template>
    </template>
  </el-menu>
</template>

<script lang="ts" setup>
import { watch, shallowReactive } from "vue";

import { useVueRouter } from "@assets/useHook";

withDefaults(
  defineProps<{
    list: any[];
    isGroup?: boolean;
  }>(),
  {
    list: () => [],
    isGroup: false,
  }
);
const { route } = useVueRouter();
const initData = shallowReactive({
  defaultMenu: "",
});
watch(
  route,
  (value) => {
    if (value.meta.defaultActiveMenu) {
      // 类似列表的详情页才会走这
      (initData.defaultMenu as any) = value.meta.defaultActiveMenu;
    } else {
      initData.defaultMenu = value.path;
    }
  },
  {
    immediate: true,
  }
);
</script>

<style lang="scss" scoped>
.custom-sub-menu {
  flex: 0 0 170px;
  margin-top: 2px;
  padding: 10px 10px;
  border-right: none;

  :deep(.el-menu-item) {
    height: 40px;
    padding: 0 0 0 15px !important;
    margin-bottom: 15px;

    &:hover,
    &.is-active {
      color: #0056ff;
      border-radius: 4px;
    }
  }

  :deep(.el-menu-item-group__title) {
    line-height: 40px;
    padding: 0 8px !important;
  }
}
</style>
